// slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)map((row, index).
export default function AdminTable({ header = hdr, row = bd, onPress }) {
  const rows = row && Object.keys(row).toString().startsWith(0) ? row : [row]
  const [page, setPage] = React.useState(0)
  const [rowsPerPage, setRowsPerPage] = React.useState(5)

  const handleChangePage = (event, newPage) => {
    setPage(newPage)
  }

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10))
    setPage(0)
  }

  return (
    <Box sx={{ width: '100%' }}>
      <TableTemplate header={header}>
        {rows
          .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
          .map((d, i) => (
            <TableRow
              key={i}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell> {d.fullName} </TableCell>
              <TableCell> {d.email} </TableCell>
              <TableCell> {d.role} </TableCell>
              <TableCell sx={{ color: d.is_disabled ? 'red' : 'green' }}>
                {d.is_disabled ? 'Inactive' : 'Active'}{' '}
              </TableCell>
              <TableCell>
                <Tooltip title="View">
                  <IconButton onClick={() => onPress(d)}>
                    <RemoveRedEyeIcon />
                  </IconButton>
                </Tooltip>
              </TableCell>
            </TableRow>
          ))}
      </TableTemplate>
      <TablePagination
        rowsPerPageOptions={[5, 10, 15, 20]}
        component="div"
        count={rows.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={handleChangePage}
        onRowsPerPageChange={handleChangeRowsPerPage}
      />
    </Box>
  )
}
