import React from 'react'
// import AddUser from '@mui/icons-material/PersonAddAlt1TwoTone'
import Chart from '@mui/icons-material/BarChart'
import Divider from '@mui/material/Divider'
import UseAppContext from '../../context/useAppContext'
import { useNavigate } from 'react-router-dom'
import ListParent from '../../component/bars/drawer/ListParent'
import LI from '../../component/bars/drawer/LI'
import { GasMeter, MailOutline } from '@mui/icons-material'

function MPSide({ open }) {
  const { user, active, adjustMany } = React.useContext(UseAppContext)
  const { user_key } = user || {}
  const navigate = useNavigate()

  return (
    <ListParent>
      <LI
        open={open}
        text={'Dashboard'}
        active={active === 'dashboard'}
        Icon={Chart}
        onPress={() => {
          adjustMany({ active: 'dashboard' })
          return navigate(`/${user_key}/dashboard`)
        }}
      />
      <Divider />

      <LI
        open={open}
        text={'Metering Allocation'}
        Icon={GasMeter}
        active={active === 'store'}
        onPress={() => {
          adjustMany({ active: 'store' })
          navigate(`/${user_key}/store`)
        }}
      />

      <LI
        open={open}
        text={'Activities'}
        Icon={MailOutline}
        active={active === 'logs'}
        onPress={() => {
          adjustMany({ active: 'logs' })
          navigate(`/${user_key}/logs`)
        }}
      />
      <Divider />
    </ListParent>
  )
}

export default MPSide
