import { Typography } from '@mui/material'
import Box from '@mui/material/Box'
import React from 'react'
import { useNavigate } from 'react-router-dom'
import Loading from '../../../component/alerts_boxes/Loading'
import { ErrorSnackbar } from '../../../component/alerts_boxes/SnackBar'
import WrappedInBars from '../../../component/bars/WrappedInBars'
import connect from '../../../context/connect_to_server'
import UseAppContext from '../../../context/useAppContext'
import MPSide from '../DsSide'
import StoreTable from './StoreTable'

function StoreItem() {
  const { adjustMany, user } = React.useContext(UseAppContext)
  const { user_key } = user || {}
  const navigate = useNavigate()
  const [data, setData] = React.useState(null)
  const [loading, setLoading] = React.useState(false)
  const [errors, setErrors] = React.useState(null)

  React.useEffect(() => {
    ;(async function () {
      adjustMany({ active: 'store' })
      setLoading(true)
      const res = await connect.GET({
        urlRoute: 'disco',
        params: 'search=STORE',
      })
      if (res.success) {
        setData(res.data)
        setLoading(false)
      } else {
        setLoading(false)
        setErrors(`You don't have any item in your virtual store`)
      }
    })()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  return (
    <WrappedInBars UL={MPSide}>
      <Box marginY={10}>
        <Typography variant="h4" component={'h6'} marginBottom={1}>
          Unallocated Meter Records
        </Typography>
        {data && (
          <StoreTable
            row={data}
            onPress={(da) => {
              adjustMany({ store_items: da })
              return navigate(`/${user_key}/store-record`)
            }}
          />
        )}
        {loading && <Loading open={loading} />}
        {errors && <ErrorSnackbar msg={errors} />}
      </Box>
    </WrappedInBars>
  )
}

export default StoreItem
