import {
  AJDUSTMANY,
  REMOVE_REQUIRED_ITEMS,
  REQUIRED_ITEMS,
} from './MagicString'

const UseAppReducer = (state, action) => {
  const { type, payload } = action || {}
  switch (type) {
    case REQUIRED_ITEMS:
      return {
        ...state,
        required_items: [...state.required_items, payload],
      }
    case REMOVE_REQUIRED_ITEMS:
      return {
        ...state,
        required_items:
          state.required_items.length > 0
            ? state.required_items.filter((d) => d.id !== payload)
            : state.required_items,
      }

    case AJDUSTMANY:
      return {
        ...state,
        ...payload,
      }

    default:
      return state
  }
}

export default UseAppReducer
