import { GetUnique } from '../../../helper/essential'

export const map_prov = (data) => {
  const {
    single,
    three,
    inStoreCount,
    outOfStoreCount,
    total,
    allocations,
    customer_count,
    customer_allocated_stats,
    customer_unallocated_stats,
    installation_count,
    map_provision,
    replacementRegistered,
    replacementTreated,
    pendingReplacement,
    // ...res
  } = data || {}

  //   NAMES
  const map_names = GetUnique(map_provision.map((d) => d.uploadedBy))
  const disco_names = GetUnique(allocations.map((d) => d.receiver))

  //      TRANSACTION
  const transaction = allocations.map((d) => {
    return {
      provider: d.receiver,
      receiver: d.provider,
      label0: d.receiver,
      label1: d.provider,
      sent: d.total_allocated,
      received: d.total_installed,
    }
  })

  return {
    header_stats: {
      map_count: 1,
      disco_count: allocations.length,
      map_total_uploads: total,
      total_meter: total,
      map_allocation: outOfStoreCount,
      disco_allocations: outOfStoreCount,
      total_meter_applicant: customer_count,
      metered_applicant: customer_allocated_stats,
      unmetered_applicant: customer_unallocated_stats,
      metersOnTransitCount: 0,
      unAllocatedMeterCount: inStoreCount,
      installation_count: installation_count,
      replacementRegistered,
      replacementTreated,
      pendingReplacement,
    },
    transaction,
    phase: { single, three },
    map_names,
    disco_names,
  }
}

export const disco_prov = (data) => {
  const {
    single,
    three,
    inStoreCount,
    outOfStoreCount,
    total,
    allocations,
    customer_count,
    customer_allocated_stats,
    customer_unallocated_stats,
    installation_count,
    disco_provision,
    replacementRegistered,
    replacementTreated,
    pendingReplacement,
    // ...res
  } = data || {}

  //   NAMES
  const disco_names = GetUnique(disco_provision.map((d) => d.receiver))
  const installer_names = GetUnique(allocations.map((d) => d.installer))

  //      TRANSACTION
  const transaction = disco_provision.map((d) => {
    const insl =
      d.single_phase_sent_and_install + d.three_phase_sent_and_install
    return {
      receiver: d.receiver,
      provider: d.provider,
      label0: d.receiver,
      label1: d.provider,
      sent: d.total_received,
      received: insl,
    }
  })

  return {
    header_stats: {
      map_count: 1,
      disco_count: allocations.length,
      map_total_uploads: total,
      total_meter: total,
      map_allocation: outOfStoreCount,
      disco_allocations: outOfStoreCount,
      total_meter_applicant: customer_count,
      metered_applicant: customer_allocated_stats,
      unmetered_applicant: customer_unallocated_stats,
      metersOnTransitCount: 0,
      unAllocatedMeterCount: inStoreCount,
      installation_count: installation_count,
      replacementRegistered,
      replacementTreated,
      pendingReplacement,
    },
    transaction,
    phase: { single, three },
    installer_names,
    disco_names,
  }
}
