export const csvDownload = ({ data, structure, headings, title }) => {
  if (data.length !== 0 && structure) {
    const dataStructure = data.map((row) => structure(row))
    const downloadableData = objectToCsv(dataStructure)
    download(downloadableData)
  }

  function objectToCsv(data) {
    const csvRow = []
    // Headers
    const headers = Object.keys(data[0])
    csvRow.push((headings || headers).join(','))

    // Loop through Data
    for (const row of data) {
      const value = headers.map((head) => {
        const escape = ('' + row[head]).replace(/"/g, '\\"')
        return `"${escape}"`
      })

      csvRow.push(value.join(','))
    }

    return csvRow.join('\n')
  }

  function download(data) {
    const blob = new Blob([data], { type: 'text/csv' })
    const url = window.URL.createObjectURL(blob)
    const a = document.createElement('a')
    a.setAttribute('hidden', '')
    a.setAttribute('href', url)
    a.setAttribute('download', `${title || 'Download'}.csv`)
    document.body.appendChild(a)
    a.click()
    document.body.removeChild(a)
  }
}
