import Grid from '@mui/material/Grid'
import Box from '@mui/material/Box'
import React from 'react'
import TextInput from '../../../../component/input/TextInput'
import { BTN, CHL } from '../../../../component/input/InputEssential'
import { Typography } from '@mui/material'
import SelectAutoWidth from '../../../../component/input/SelectComponent'

function OtherInformation({ parent_form, onPageChange, handleChange }) {
  const [form, setForm] = React.useState({ ...parent_form.oth_info_data })
  const { role, abbrv, right_to_share_profile } = form
  const onChange = (name, value) => setForm({ ...form, [name]: value })

  return (
    <Box marginY={2}>
      <Typography variant="h6" component={'p'} marginTop={2}>
        Other Relevant Information
      </Typography>
      <Grid container spacing={2}>
        <INPUT
          onChange={onChange}
          name={'abbrv'}
          value={abbrv}
          label={"Company's Acronym"}
          required={true}
        />
        <Grid item xs={4}>
          <SelectAutoWidth
            handleInputChange={onChange}
            name={'role'}
            value={role}
            label={'Select User Role *'}
            items={[
              'ADMIN',
              'DISCO',
              'MAP',
              'NERC',
              'NESI',
              'CBN',
              'INSTALLER',
              'MAP:INSTALLER',
              'DISCO:INSTALLER',
              'SITE-VERIFICATION-OFFICER',
              'LECAN',
              'GUEST',
              'OTHER',
            ]}
            size={'100%'}
          />
        </Grid>
      </Grid>

      <CHL
        label={'Grant user the write to share profile'}
        handleChange={onChange}
        checked={right_to_share_profile}
        name={'right_to_share_profile'}
      />

      <BTN
        onSubmit={() =>
          onPageChange('coverage', true, undefined, 'oth_info_data', form)
        }
        disabled={!abbrv || !role}
        onCancel={() => onPageChange('locs', true, true, 'oth_info_data', form)}
      />
    </Box>
  )
}

function INPUT({
  onChange,
  name,
  value,
  required,
  span = 4,
  label,
  input_type,
}) {
  return (
    <Grid item xs={span}>
      <TextInput
        onChange={onChange}
        name={name}
        value={value}
        required={required}
        label={label}
        input_type={input_type}
      />
    </Grid>
  )
}

export default OtherInformation
