import Search from '@mui/icons-material/Search'
import { Box, IconButton } from '@mui/material'
import React, { useState } from 'react'
import { COLORS } from '../../../constants'
import UseAppContext from '../../../context/useAppContext'

function SearchBar({ onPress, data, holder = 'Search & filter items...' }) {
  const { adjustMany } = React.useContext(UseAppContext)
  const [value, setValue] = useState('')
  const onChange = (e) => setValue(e.target.value)
  const [focus, setFocus] = React.useState(false)

  return (
    <Box
      className="search-bar"
      style={{
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        border: `${focus ? '2px' : '1px'} solid ${COLORS.primary}`,
        padding: 0,
      }}
    >
      <input
        type={'search'}
        value={value}
        onChange={(e) => {
          if (data) {
            const regex = new RegExp(e.target.value, 'i')
            const fiText = data.filter((el) => regex.test(el.fullName))
            setValue(e.target.value)
            adjustMany({ list: e.target.value !== '' ? fiText : data })
          }
          onChange(e)
        }}
        onFocus={() => setFocus(true)}
        onBlur={() => setFocus(false)}
        style={{
          flex: 4,
          border: 'none',
          outline: 'none',
          padding: '1rem',
          fontSize: '1rem',
        }}
        placeholder={holder}
      />
      <Box className="search-bar-icon">
        <IconButton
          onClick={() => onPress && value.length > 0 && onPress(value)}
        >
          <Search />
        </IconButton>
      </Box>
    </Box>
  )
}

export default SearchBar
