import { Typography } from '@mui/material'
import Box from '@mui/material/Box'
import React from 'react'
import { useNavigate } from 'react-router-dom'
import Loading from '../../../component/alerts_boxes/Loading'
import { ErrorSnackbar } from '../../../component/alerts_boxes/SnackBar'
import WrappedInBars from '../../../component/bars/WrappedInBars'
import connect from '../../../context/connect_to_server'
import UseAppContext from '../../../context/useAppContext'
import MPSide from '../MPSide'
import AllocationCards from './AllocationCards'
import EachStoreTable from './EachStoreTable'

function EachStoreItem() {
  const { adjustMany, user, store_items } = React.useContext(UseAppContext)
  const { _id } = store_items || {}
  const { user_key } = user || {}
  const navigate = useNavigate()
  const [data, setData] = React.useState(null)
  const [loading, setLoading] = React.useState(false)
  const [errors, setErrors] = React.useState(false)

  React.useEffect(() => {
    ;(async function () {
      adjustMany({ active: 'store' })
      setLoading(true)
      const res = await connect.GET({
        urlRoute: 'map',
        params: `destinationStore=${_id}&search=ITEMS`,
      })

      if (res.success) {
        setData(res.data)
        setLoading(false)
      } else {
        setLoading(false)
        setErrors(res.error)
      }
    })()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  return (
    <WrappedInBars UL={MPSide}>
      <Box marginY={10}>
        <Typography variant="h4" component={'h6'} marginBottom={1}>
          Meter Statistics in {_id} Store
        </Typography>

        {data && <AllocationCards data={data.all} />}
        {data && (
          <Typography variant="h4" component={'h6'} marginBottom={1}>
            Unallocated Meter in Cartons
          </Typography>
        )}
        {data && (
          <EachStoreTable
            row={data.each}
            onPress={(da) => {
              adjustMany({ store_items: { ...da, ...store_items } })
              return navigate(`/${user_key}/allocate`)
            }}
          />
        )}
        {loading && <Loading open={loading} />}
        {errors && <ErrorSnackbar msg={errors} />}
      </Box>
    </WrappedInBars>
  )
}

export default EachStoreItem
