import { Button, Paper } from '@mui/material'
import Sad from '@mui/icons-material/SentimentDissatisfied'
import React from 'react'

const Blank = ({ Press, msg = 'No Page Found', btn_title = 'Reload' }) => {
  return (
    <div
      style={{
        backgroundColor: 'rgba(139, 139, 139, 0.3)',
        height: '100vh',
        justifyContent: 'center',
        alignItems: 'center',
        display: 'flex',
      }}
    >
      <Paper
        style={{
          padding: 20,
          height: '63vh',
          width: 350,
          margin: '0 auto',
          borderRadius: '0.5rem',
        }}
        sx={{
          justifyContent: 'center',
          alignItems: 'center',
          display: 'flex',
          flexDirection: 'column',
        }}
      >
        <Sad sx={{ fontSize: '2rem', color: 'red' }} />
        <h4>{msg}</h4>
        <Button onClick={Press}>{btn_title}</Button>
      </Paper>
    </div>
  )
}

export default Blank
