import Box from '@mui/material/Box'
import React from 'react'
import SelectAutoWidth from '../../../component/input/SelectComponent'
import { BTN } from '../../../component/input/InputEssential'
import TextInput from '../../../component/input/TextInput'

function SHBasicInformation({ parent_form, onPageChange }) {
  const { individual } = parent_form || {}
  const [form, setForm] = React.useState({ ...parent_form.basic_data })
  const { fname, lname, email, telephone, identity_id, identity_type } = form
  const onChange = (name, value) => setForm({ ...form, [name]: value })

  return (
    <Box>
      {/* <Grid container spacing={2}> */}
      <INPUT
        onChange={onChange}
        name={'fname'}
        value={fname}
        label={individual ? 'First Name *' : 'Company Full Name'}
        required={true}
        span={individual ? 4 : 8}
      />
      {individual && (
        <INPUT
          onChange={onChange}
          name={'lname'}
          value={lname}
          label={'Other Name'}
        />
      )}
      {/* </Grid> */}

      {/* <Grid container spacing={2}> */}
      <INPUT
        onChange={onChange}
        name={'email'}
        value={email}
        required={true}
        label={'Email Address'}
        input_type="email"
      />
      <INPUT
        onChange={onChange}
        name={'telephone'}
        value={telephone}
        required={true}
        label={'Telephone'}
      />
      {/* </Grid> */}

      {/* <Grid container spacing={2}>
        <Grid item xs={4}> */}
      <SelectAutoWidth
        handleInputChange={onChange}
        name={'identity_type'}
        value={identity_type}
        label={'Select Means of Identity *'}
        items={[
          'Voters Card',
          'Driver Licence',
          'International Passport',
          'Certificate of Incorporation',
          'Other',
        ]}
        size={'100%'}
      />
      {/* </Grid> */}
      <INPUT
        onChange={onChange}
        name={'identity_id'}
        value={identity_id}
        required={true}
        label={'Identitiy ID'}
      />
      {/* </Grid> */}

      <BTN
        onSubmit={() =>
          onPageChange('locs', true, undefined, 'basic_data', form)
        }
        disabled={
          !email || !telephone || !fname || !identity_id || !identity_type
        }
      />
    </Box>
  )
}

function INPUT({
  onChange,
  name,
  value,
  required,
  span = 4,
  label,
  input_type,
}) {
  return (
    // <Grid item xs={span}>
    <TextInput
      onChange={onChange}
      name={name}
      value={value}
      required={required}
      label={label}
      input_type={input_type}
    />
    // </Grid>
  )
}

export default SHBasicInformation
