import { Box, Divider, Paper, Typography } from '@mui/material'
import React from 'react'
import { COLORS } from '../../../constants'
import UseAppContext from '../../../context/useAppContext'
import { ErrorSnackbar } from '../../../component/alerts_boxes/SnackBar'

function MidDownSide({ data = [] }) {
  const { user } = React.useContext(UseAppContext)
  const { role } = user || {}
  const nd = role === 'DISCO'
  const [errors, setErrors] = React.useState(null)
  const { disco_names, installer_names } = data || {}
  const nn = nd ? installer_names : disco_names || []
  const names = nn.length > 0 ? nn : undefined

  // const adjustView = (k) => {
  //   const { assets_exchange, map_provision, disco_provision, customer_stats } =
  //     jsn || {}

  //   if (assets_exchange && map_provision && disco_provision) {
  //     const itm2 = disco_prov(
  //       assets_exchange,
  //       k,
  //       disco_provision,
  //       map_provision,
  //       customer_stats
  //     )

  //     adjustMany({ cbs: itm2, dsk: k.toLowerCase(), signal: 'DISCO' })
  //   } else setErrors(`No view found for: ${k}`)
  // }

  return (
    <Paper
      sx={{
        width: '16rem',
        height: '55vh',
        zIndex: 10,
        right: 10,
        top: '42%',
        position: 'absolute',
        borderRadius: '0.5rem',
      }}
      elevation={12}
    >
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'flex-start',
          alignItems: 'center',
        }}
      >
        <Typography
          sx={{
            padding: 1,
            color: COLORS.secondary,
            fontSize: '0.8rem',
            textTransform: 'uppercase',
          }}
          variant={'h6'}
          component={'h6'}
        >
          {nd ? 'Installer Names' : 'Disco Names'}:{' '}
          <strong>{names?.length || 0}</strong>
        </Typography>

        {/* {signal === 'DISCO' && (
          <IconButton onClick={clear} sx={{ marginLeft: 1 }}>
            <Sync sx={{ fontSize: '0.9rem' }} />
          </IconButton>
        )} */}
      </Box>

      <Divider />

      <Box
        sx={{
          padding: '0.2rem 1rem 0.5rem',
          display: 'flex',
          // justifyContent: 'space-around',
          alignItems: 'flex-start',
        }}
      >
        <Box>
          {names &&
            names
              .slice(0, 7)
              .map((d, i) => <Typography key={i}>- {d}</Typography>)}
        </Box>
        <Box>
          {names &&
            names
              .slice(7, 11)
              .map((d, i) => <Typography key={i}>{d}</Typography>)}
        </Box>
      </Box>
      {errors && <ErrorSnackbar msg={errors} cancel={() => setErrors(null)} />}
    </Paper>
  )
}

export default MidDownSide
