import Box from '@mui/material/Box'
import React from 'react'
import SelectAutoWidth from '../../../component/input/SelectComponent'
import TextInput from '../../../component/input/TextInput'
import { BTN } from '../../../component/input/InputEssential'
import { Typography } from '@mui/material'
import UseAppContext from '../../../context/useAppContext'

function SHOtherInformation({ parent_form, onPageChange }) {
  const { user } = React.useContext(UseAppContext)
  const { role: rl } = user || {}
  const [form, setForm] = React.useState({ ...parent_form.oth_info_data })
  const { role, abbrv } = form
  const onChange = (name, value) => setForm({ ...form, [name]: value })

  const d = ['DISCO:INSTALLER', 'SITE-VERIFICATION-OFFICER']
  const m = ['INSTALLER', 'MAP:INSTALLER']
  const itm = rl === 'MAP' ? m : rl === 'DISCO' ? d : []

  return (
    <Box marginY={2}>
      <Typography variant="h6" component={'p'} marginTop={2}>
        Other Relevant Information
      </Typography>
      {/* <Grid container spacing={2}> */}
      <INPUT
        onChange={onChange}
        name={'abbrv'}
        value={abbrv}
        label={"Company's Acronym"}
        required={true}
      />
      {/* <Grid item xs={4}> */}
      <SelectAutoWidth
        handleInputChange={onChange}
        name={'role'}
        value={role}
        label={'Select User Role *'}
        items={itm}
        size={'100%'}
      />
      {/* </Grid>
      </Grid> */}

      <BTN
        onSubmit={() =>
          onPageChange('finish', true, undefined, 'oth_info_data', form)
        }
        disabled={!abbrv || !role}
        onCancel={() => onPageChange('locs', true, true, 'oth_info_data', form)}
      />
    </Box>
  )
}

function INPUT({
  onChange,
  name,
  value,
  required,
  span = 4,
  label,
  input_type,
}) {
  return (
    // <Grid item xs={span}>
    <TextInput
      onChange={onChange}
      name={name}
      value={value}
      required={required}
      label={label}
      input_type={input_type}
    />
    // </Grid>
  )
}

export default SHOtherInformation
