import Paper from '@mui/material/Paper'
import Typography from '@mui/material/Typography'
import Box from '@mui/material/Box'
import UserIcon from '@mui/icons-material/Person'
import { Avatar } from '@mui/material'

const CARD = ({ title = 'Total Users', value = 0, Icon }) => {
  return (
    <Paper
      elevation={1}
      style={{
        height: 140,
        display: 'flex',
        alignItems: 'center',
      }}
    >
      <Box
        style={{
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          padding: '1rem',
          width: '100%',
        }}
      >
        <Box>
          <Typography variant="h5" component={'h6'} marginBottom={2}>
            {title}
          </Typography>

          <Typography variant="h4" component={'h6'}>
            {value}
          </Typography>
        </Box>

        <Avatar>{Icon ? <Icon /> : <UserIcon />}</Avatar>
      </Box>
    </Paper>
  )
}

const AdminCard = ({ data }) => {
  const total = data.length
  const active =
    data.length > 0 ? data.filter((d) => d.is_disabled === false).length : 0
  const suspend =
    data.length > 0 ? data.filter((d) => d.is_disabled === true).length : 0

  return (
    <Box className="grid-3">
      <CARD value={total} />
      <CARD title={'Active Users'} value={active} />
      <CARD title={'Supended Users'} value={suspend} />
    </Box>
  )
}

export default AdminCard
