import { Paper, Typography } from '@mui/material'
import React from 'react'

function SetBoxs({ title = 'Box Title', click, name, label, open = false }) {
  return (
    <Paper
      sx={{
        height: '3rem',
        width: '100%',
        bgcolor: open && '#f2f2f2',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        cursor: 'pointer',
        marginY: 0.5,
      }}
      elevation={1}
      onClick={() => click && click(name, label)}
    >
      <Typography>{title}</Typography>
    </Paper>
  )
}

export default SetBoxs
