export const Sum = (array) => {
  if (!array) return 0
  if (array[0] == null) return 0

  let all = 0
  for (let i = 0; i < array.length; i++) {
    all += array[i]
  }

  return all
}

export const GetUnique = (array) => {
  if (!array) return []
  return [...new Set(array)]
}

export const create_queries = (obj) => {
  if (!obj) return {}
  let keys = ''
  let aggs = '&'
  const key = Object.keys(obj)
  const value = Object.values(obj)

  for (let i = 0; i < key.length; i++) {
    if (i < key.length - 1) {
      aggs += `${key[i]}=${value[i]}&`
      keys += `${key[i]},`
    } else {
      aggs += `${key[i]}=${value[i]}`
      keys += `${key[i]}`
    }
  }
  return { keys, aggs }
}

export const get_now = () => {
  const date = new Date()
  const mm =
    date.getMinutes() <= 10 ? `0${date.getMinutes()}` : date.getMinutes()
  const hr = date.getHours() <= 10 ? `0${date.getHours()}` : date.getHours()
  const yr = date.getFullYear()
  const dae = date.getDate() <= 10 ? `0${date.getDate()}` : date.getDate()
  const mo = date.getMonth() <= 10 ? `0${date.getMonth()}` : date.getMonth()
  const pd = hr < 12 ? 'AM' : 'PM'
  let now

  if (hr < 12) now = 'Good Morning'
  else if (hr > 11 && hr <= 16) now = 'Good Afternoon'
  else if (hr > 16) now = 'Good Evening'
  const tt = `${dae}/${mo}/${yr} ${hr}:${mm} ${pd}`

  return { now, time: tt }
}

export const ar_ob = (ar) => {
  if (!ar) return []

  const keeExt = ar.map((el, index) => {
    const kee = (Math.random() * 3478437).toString()
    return { _id: `${kee}${index}${el}`, data: el }
  })

  return keeExt
}

export const _ide = () => {
  const date = new Date()
  return (
    Math.ceil(
      Math.random() * 141039900 +
        date.getMilliseconds() +
        date.getDate() +
        date.getSeconds() +
        date.getFullYear() +
        date.getMonth()
    ) + 40000000000
  )
}

export const NumberRefactor = (num) => {
  if (!num || typeof num !== 'number') return 0
  const numberCount = num.toString().length
  // quadrillion, quintrillion, sextillion, septillion, octillion, nonillion, and decillion.

  if (numberCount <= 3) return num.toFixed(0)
  else if (numberCount >= 4 && numberCount <= 6)
    return `${(num / 1000).toFixed(0)}K`
  else if (numberCount >= 7 && numberCount < 9)
    return `${(num / 1000000).toFixed(0)}M`
  else if (numberCount >= 9 && numberCount < 12)
    return `${(num / 100000000).toFixed(0)}Bn`
  else if (numberCount >= 12 && numberCount < 15)
    return `${(num / 100000000000).toFixed(0)}Tr`
  else if (numberCount >= 15 && numberCount < 18)
    return `${(num / 100000000000000).toFixed(0)}QUA`
  else if (numberCount >= 18 && numberCount < 21)
    return `${(num / 100000000000000000).toFixed(0)}QUIN`
  else return 'N/A'
}

export const numberWithCommas = (x) => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
}

export const isArray = (obj) => Object.keys(obj).toString().startsWith('0')
export const isObject = (obj) => {
  const keys = Object.keys(obj)
  if (!keys.toString().startsWith('0') && keys.length > 0) return true
  else return false
}

export const date_config = () => {
  const dt = new Date()
  const dd = dt.getDate() < 10 ? `0${dt.getDate()}` : dt.getDate()
  const mm = dt.getMonth() < 10 ? `0${dt.getMonth()}` : dt.getMonth()
  const yy = dt.getFullYear()
  const et = `${yy}-${mm}-${dd}`

  return { day: dd, monnth: mm, year: yy, fdate: et }
}
