import TableCell from '@mui/material/TableCell'
import TableRow from '@mui/material/TableRow'

import { Box, IconButton, TablePagination, Tooltip } from '@mui/material'
import React from 'react'
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye'
import TableTemplate from '../../../component/tables/simple_table/TableTemplate'

const hdr = [
  'Store ID',
  'Carton ID',
  'Meter Phase',
  'Meter Provider',
  'Manufacturer Name',
  'Default Unit',
  'Total Pieces',
  'Action',
]

const bd = [
  {
    cartonID: '23',
    meterOwner: '39e',
    storeID: '38',
    destinationStore: '892',
    phase: '3',
    manufacturerName: '39',
    defaultUnit: '58',
    pop: '39',
  },
]
// slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)map((row, index).
export default function EachStoreTable({ header = hdr, row = bd, onPress }) {
  const rows = row && Object.keys(row).toString().startsWith(0) ? row : [row]
  const [page, setPage] = React.useState(0)
  const [rowsPerPage, setRowsPerPage] = React.useState(5)

  const handleChangePage = (event, newPage) => {
    setPage(newPage)
  }

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10))
    setPage(0)
  }

  return (
    <Box sx={{ width: '100%' }}>
      <TableTemplate header={header}>
        {rows
          .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
          .map((d, i) => (
            <TableRow
              key={i}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              {/* cartonID, meterOwner, storeID, destinationStore,
                        phase, manufacturerName, defaultUnit, pop */}
              <TableCell> {d.storeID} </TableCell>
              <TableCell> {d.cartonID} </TableCell>
              <TableCell> {d.meterOwner} </TableCell>
              <TableCell> {d.phase} </TableCell>
              <TableCell> {d.manufacturerName || 'Not available'} </TableCell>
              <TableCell> {d.defaultUnit} </TableCell>
              <TableCell> {d.pop} </TableCell>
              <TableCell>
                <Tooltip title="Proceed to Allocation">
                  <IconButton onClick={() => onPress(d)}>
                    <RemoveRedEyeIcon />
                  </IconButton>
                </Tooltip>
              </TableCell>
            </TableRow>
          ))}
      </TableTemplate>
      <TablePagination
        rowsPerPageOptions={[5, 10, 15, 20]}
        component="div"
        count={rows.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={handleChangePage}
        onRowsPerPageChange={handleChangeRowsPerPage}
      />
    </Box>
  )
}
