import { Box, Divider } from '@mui/material'
import React from 'react'
import WrappedInBars from '../../../../../component/bars/WrappedInBars'
import UseAppContext from '../../../../../context/useAppContext'
import AdsSide from '../../../AdsSide'
import BodyView from './BodyView'
import HeaderView from './HeaderView'

function UserView({ data }) {
  const { view_user } = React.useContext(UseAppContext)

  // React.useEffect(() => {
  //   ;(function () {
  //     adjustMany({ active: 'dashboard' })
  //   })()
  // eslint-disable-next-line react-hooks/exhaustive-deps
  // }, [])

  return (
    <WrappedInBars UL={AdsSide}>
      <Box marginY={10}>
        <HeaderView data={view_user} />
        <Divider />
        <BodyView data={view_user} />
      </Box>
    </WrappedInBars>
  )
}

export default UserView
