import React from 'react'
import Box from '@mui/material/Box'
import Button from '@mui/material/Button'
import Dialog from '@mui/material/Dialog'
import DialogActions from '@mui/material/DialogActions'
import DialogContent from '@mui/material/DialogContent'
import DialogTitle from '@mui/material/DialogTitle'
import InputLabel from '@mui/material/InputLabel'
import OutlinedInput from '@mui/material/OutlinedInput'
import MenuItem from '@mui/material/MenuItem'
import FormControl from '@mui/material/FormControl'
import Select from '@mui/material/Select'

export default function ModelSelect({
  title = 'Open Select Dialog',
  Sub = 'Select Franchise Area',
  items = [],
  onChange,
  value = '',
  name,
}) {
  const [open, setOpen] = React.useState(false)
  const [text, setText] = React.useState('')

  const handleChange = (event) => {
    console.log(event.target.value)
    setText(event.target.value)
  }

  const handleClickOpen = () => {
    setOpen(true)
  }

  const handleClose = (event, reason) => {
    if (reason !== 'backdropClick') {
      setOpen(false)
    }
  }

  return (
    <div style={{ marginTop: 5, marginBottom: 5 }}>
      <Button variant="contained" onClick={handleClickOpen}>
        {title}
      </Button>
      <Dialog disableEscapeKeyDown open={open} onClose={handleClose} fullWidth>
        <DialogTitle>{title}</DialogTitle>
        <DialogContent>
          <Box component="form" sx={{ display: 'flex', flexWrap: 'wrap' }}>
            <FormControl sx={{ m: 1, minWidth: 120, width: '100%' }}>
              <InputLabel id="demo-dialog-select-label">{Sub}</InputLabel>
              <Select
                labelId="demo-dialog-select-label"
                id="demo-dialog-select"
                value={text}
                onChange={handleChange}
                input={<OutlinedInput label={`${Sub}`} />}
              >
                <MenuItem value="">
                  <em>None</em>
                </MenuItem>
                {items.map((d, i) => (
                  <MenuItem value={d} key={`${d}:${i}`}>
                    {d}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Cancel</Button>
          <Button
            onClick={() => {
              onChange(name, text)
              return handleClose()
            }}
          >
            Add
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}
