import { Box } from '@mui/material'
import React from 'react'
import SwipeableTextMobileStepper from '../../../../component/imgslider/ImgSlider'
import ProgressSteps from '../../../../component/ProgressSteps'
import { assets } from '../../../../constants'
import connect from '../../../../context/connect_to_server'

function MeterMovement({ data }) {
  const {
    mapSnapShot,
    discoSnapShot,
    afterInstallationImage,
    beforeInstallationImage,
    meterOwner,
    disco_acknowledgement_by,
    discoStore,
    destinationStore,
    discoAllocationTo,
    propertyID,
  } = data || {}

  const im = [
    {
      label: 'Factory Meter Snapshot',
      imgPath: connect.img(mapSnapShot) || assets.not_available,
    },
    {
      label: 'Disco Snapshot',
      imgPath: connect.img(discoSnapShot) || assets.not_available,
    },
    {
      label: 'Before Installation Snapshot',
      imgPath: connect.img(afterInstallationImage) || assets.not_available,
    },
    {
      label: 'After Installation Snapshot',
      imgPath: connect.img(beforeInstallationImage) || assets.not_available,
    },
  ]

  let current = 0
  if (connect.img(afterInstallationImage)) current = 3
  else if (connect.img(beforeInstallationImage)) current = 2
  else if (connect.img(discoSnapShot)) current = 1
  return (
    <Box
      sx={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        flexDirection: 'column',
      }}
    >
      <ProgressSteps
        activeStep={current}
        steps={[
          {
            name: meterOwner,
            id: destinationStore || 'Not avaliable',
            from: 'From (MAP)',
          },
          {
            name: disco_acknowledgement_by,
            id: discoStore || 'Not avaliable',
            from: 'To (DISCO)',
          },
          {
            name: discoAllocationTo,
            id: 'Not avaliable',
            from: 'To (INSTALLER)',
          },
          { name: propertyID, id: 'Not avaliable', from: 'To (PROPERTY)' },
        ]}
        names={['name', 'id', 'from', 'to']}
      />
      <Box sx={{ marginY: 2 }}>
        <SwipeableTextMobileStepper img={im} />
      </Box>
    </Box>
  )
}

export default MeterMovement
