import React from 'react'
import AddUser from '@mui/icons-material/MailOutline'
import Chart from '@mui/icons-material/BarChart'
import Book from '@mui/icons-material/Book'
import Divider from '@mui/material/Divider'
import UseAppContext from '../../context/useAppContext'
import { useNavigate } from 'react-router-dom'
import ListParent from '../../component/bars/drawer/ListParent'
import LI from '../../component/bars/drawer/LI'

function CBSide({ open }) {
  const { user, active, adjustMany } = React.useContext(UseAppContext)
  const { user_key } = user || {}
  const navigate = useNavigate()

  return (
    <ListParent>
      <LI
        open={open}
        text={'Dashboard'}
        active={active === 'dashboard'}
        Icon={Chart}
        onPress={() => {
          adjustMany({ active: 'dashboard' })
          return navigate(`/`)
        }}
      />
      <Divider />
      <LI
        open={open}
        text={'Activities'}
        Icon={AddUser}
        active={active === 'logs'}
        onPress={() => {
          adjustMany({ active: 'logs' })
          navigate(`/${user_key}/logs`)
        }}
      />

      {/* <LI
        open={open}
        text={'Dataset'}
        Icon={Book}
        active={active === 'dataset'}
        onPress={() => {
          adjustMany({ active: 'dataset' })
          navigate(`/${user_key}/dataset`)
        }}
      /> */}
      <Divider />
    </ListParent>
  )
}

export default CBSide
