import { Box } from '@mui/material'
import React from 'react'
import { COLORS } from '../../../../constants'
import { BarChart, HorizontalChart } from './GraphHub'

function GraphComponent({ data = [{}] }) {
  const received = data.map((d) => d.received)
  const sent = data.map((d) => d.sent)
  const sender = data.map((d) => d.provider)
  const receiver = data.map((d) => d.receiver)
  const showBar = data.length > 5

  return (
    <Box>
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'flex-start',
          alignItems: 'center',
        }}
      >
        <InOut />
        <InOut
          title="Received"
          ln={COLORS.light_primary}
          bg={'rgba(215, 189, 223, 0.7)'}
        />
      </Box>
      {showBar ? (
        <BarChart
          DATA0={sent}
          DATA1={received}
          LABELs0={sender}
          LABELs1={receiver}
          LABEL0={'MAP'}
          LABEL1={'DISCO'}
        />
      ) : (
        <HorizontalChart
          DATA0={sent}
          DATA1={received}
          LABELs0={sender}
          LABELs1={receiver}
          LABEL0={'MAP'}
          LABEL1={'DISCO'}
        />
      )}
    </Box>
  )
}

function InOut({
  title = 'Sent',
  bg = 'rgb(74, 20, 140,0.6)',
  ln = COLORS.primary,
}) {
  return (
    <div
      style={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        margin: '0 1rem',
      }}
    >
      <div
        style={{
          width: 10,
          height: 10,
          borderRadius: '50%',
          backgroundColor: bg,
          border: `1px solid ${ln}`,
        }}
      ></div>
      <p
        style={{
          textTransform: 'uppercase',
          marginLeft: 10,
          fontSize: '0.8rem',
        }}
      >
        {title}
      </p>
    </div>
  )
}

export default GraphComponent
