import Box from '@mui/material/Box'
import React from 'react'
import { Typography } from '@mui/material'

function ProfileFooterView({ franchise_states }) {
  return (
    <Box marginY={1}>
      <Typography variant="h6" component={'p'} marginTop={2}>
        Coverage Area
      </Typography>
      {franchise_states.length > 0 && (
        <Box
          style={{
            display: 'grid',
            gridTemplateColumns: 'repeat(5,1fr)',
            justifyContent: 'flex-start',
            alignItems: 'center',
            border: '1px solid #ccc',
            padding: '0.5rem',
            width: '80%',
            margin: '0.8rem 0',
          }}
        >
          {franchise_states?.map((d, i) => (
            <Item key={i} title={d} />
          ))}
        </Box>
      )}
    </Box>
  )
}

function Item({ title }) {
  if (!title) return <></>
  return (
    <Box
      style={{
        display: 'flex',
        justifyContent: 'flex-start',
        alignItems: 'center',
      }}
    >
      <Typography variant="body1" component={'p'}>
        {title}
      </Typography>
    </Box>
  )
}

export default ProfileFooterView
