import { Divider } from '@mui/material'
import React from 'react'
import WrappedInBars from '../../../component/bars/WrappedInBars'
import UseAppContext from '../../../context/useAppContext'
import PropTables from './PropTables'
import SearchHeader from './SearchHeader'

function RegisteredProps({ Side }) {
  const { adjustMany, list } = React.useContext(UseAppContext)
  React.useEffect(() => {
    ;(function () {
      adjustMany({ active: 'property' })
    })()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  return (
    <WrappedInBars UL={Side}>
      <SearchHeader />
      <Divider />
      {list && <PropTables />}
    </WrappedInBars>
  )
}

export default RegisteredProps
