import { Avatar, Box, Divider, Paper, Typography } from '@mui/material'
import React from 'react'
import { COLORS } from '../../../constants'
import connect from '../../../context/connect_to_server'

function SHCards({ data }) {
  const {
    passport_url: url,
    fullName,
    role,
    telephone,
    province,
    location_coords,
  } = data || {}
  const uri = connect.img(url || undefined)
  const txt = fullName ? fullName.slice(0, 3) : 'NO'

  if (!data) return <></>

  return (
    <Paper sx={{ padding: 1 }}>
      <Box
        sx={{
          display: 'flex',
          alignItems: 'center',
          padding: 1,
        }}
      >
        {uri ? (
          <Avatar src={uri} alt="Profile Img" />
        ) : (
          <Avatar sx={{ backgroundColor: COLORS.light_primary }}>{txt}</Avatar>
        )}
        <Typography sx={{ marginX: 1 }}>{fullName || ''}</Typography>
      </Box>
      <Divider />

      <Box
        sx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
          width: '100%',
        }}
      >
        <Box>
          <Typography>
            <strong>Assign Role: </strong>
            {role || ''}
          </Typography>
          <Typography>
            <strong>Contact Info: </strong>
            {telephone || ''}
          </Typography>
        </Box>
        <Box>
          <Typography>
            <strong>Location: </strong>
            {province || ''}
          </Typography>
          <Typography>
            <strong>Coordinate: </strong>
            {location_coords || 'Not available'}
          </Typography>
        </Box>
      </Box>
    </Paper>
  )
}

export default SHCards
