import React from 'react'
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom'
import WrappedInBars from '../../component/bars/WrappedInBars'
import UseAppContext from '../../context/useAppContext'
import Logger from '../activities/Logger'
import ProfileUserView from '../profile/ProfileUserView'
import CBSide from './CBSide'
import CBDashboard from './pages/CBDashboard'
import DataSet from './pages/DataSet'
// import CBDashboard from './pages/CBDashboard'
import MeteringInfo from './pages/MeteringInfo'

export default function Cbs() {
  const { user } = React.useContext(UseAppContext)
  const { user_key } = user || {}

  const DB = () => {
    return <WrappedInBars UL={CBSide}></WrappedInBars>
  }

  return (
    <>
      <Router>
        <Routes>
          <Route path={`/`} element={<CBDashboard />} />
          <Route path={`${user_key}/home`} element={<DB />} />
          <Route
            path={`${user_key}/metering-result`}
            element={<MeteringInfo />}
          />
          <Route path={`${user_key}/dataset`} element={<DataSet />} />
          <Route path={`${user_key}/logs`} element={<Logger Side={CBSide} />} />
          <Route
            path={`${user_key}/profile`}
            element={<ProfileUserView Side={CBSide} />}
          />
        </Routes>
      </Router>
    </>
  )
}
