import { Box } from '@mui/material'
import React from 'react'
import Loading from '../../component/alerts_boxes/Loading'
import { ErrorSnackbar } from '../../component/alerts_boxes/SnackBar'
import connect from '../../context/connect_to_server'
import UseAppContext from '../../context/useAppContext'
import TopBack from './dashboard/TopBack'
import MidDown from './dashboard/MidDown'
import TopMidSide from './dashboard/TopMidSide'
import MidDownSide from './dashboard/MidDownSide'
import { map_prov, disco_prov } from './dashboard/CleanUpData'

function DsMpDashboard() {
  const { adjustMany, cbs, user } = React.useContext(UseAppContext)
  const { role } = user || {}
  const [form, setForm] = React.useState({ loading: false, errors: null })
  const nd = role === 'DISCO'

  // if (res.success) {
  React.useEffect(() => {
    ;(async function () {
      setForm({ ...form, loading: true, errors: null })
      const res = await connect.GET({
        urlRoute: nd ? 'disco' : 'map',
        data: { search: nd ? 'DISK' : 'DISK-MAP' },
      })
      console.log(res)
      if (res.success) {
        adjustMany({
          cbs: nd ? disco_prov(res.data) : map_prov(res.data),
          jsn: res.data,
          dsk: null,
          signal: null,
        })
        setForm({ ...form, loading: false, errors: null })
      } else setForm({ ...form, loading: false, errors: res.error })
    })()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const { loading, errors } = form

  return (
    <>
      {cbs && (
        <Box>
          <TopMidSide data={cbs} />
          <TopBack data={cbs} />
          <MidDown data={cbs} />
          <MidDownSide data={cbs} />
        </Box>
      )}
      {loading && <Loading open={loading} />}
      {errors && (
        <ErrorSnackbar
          msg={errors}
          cancel={() => setForm({ ...form, errors: null })}
        />
      )}
    </>
  )
}

export default DsMpDashboard
