import Paper from '@mui/material/Paper'
import Grid from '@mui/material/Grid'
import Typography from '@mui/material/Typography'
import Box from '@mui/material/Box'
import { GasMeter } from '@mui/icons-material'
import { assets, COLORS } from '../../../../constants'
import connect from '../../../../context/connect_to_server'

const Card = ({ data, onPress }) => {
  const { meterNumber, cartonID, mapSnapShot } = data || {}
  const uri = connect.img(mapSnapShot)
  console.log(uri)
  return (
    <Grid item xs={3}>
      <Paper
        elevation={3}
        className={onPress && 'btnx'}
        onClick={() => onPress && onPress(data)}
      >
        <img src={uri || assets.nft07} alt="Factory img..." className="img" />
        <Box padding={1}>
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            <GasMeter sx={{ color: COLORS.secondary, marginRight: 0.5 }} />
            <Typography
              variant="h6"
              component={'h6'}
              sx={{ color: COLORS.primary }}
            >
              Quick Review:
            </Typography>
          </Box>
          <GfWrap value={meterNumber} />
          <GfWrap value={cartonID} title={'Carton ID:'} />
        </Box>
      </Paper>
    </Grid>
  )
}

function GfWrap({ title = 'Metering ID:', value = 3932892, Icon }) {
  return (
    <Box sx={{ display: 'flex', alignItems: 'center' }}>
      {Icon ? (
        <Icon sx={{ color: COLORS.primary }} />
      ) : (
        <Typography component={'p'} variant={'body2'} marginLeft={0.5}>
          {title}
        </Typography>
      )}
      <Typography component={'p'} variant={'body2'} marginLeft={0.5}>
        {value}
      </Typography>
    </Box>
  )
}

const MeteringCard = ({ data, onPress }) => {
  return (
    <Grid container spacing={3}>
      {data && data.map((d, i) => <Card data={d} key={i} onPress={onPress} />)}
    </Grid>
  )
}

export default MeteringCard
