import { Divider } from '@mui/material'
import React from 'react'
import Loading from '../../../component/alerts_boxes/Loading'
import OpenBox from '../../../component/alerts_boxes/OpenBox'
import {
  ErrorSnackbar,
  SuccessSnackbar,
} from '../../../component/alerts_boxes/SnackBar'
import SelectAutoWidth from '../../../component/input/SelectComponent'
import TextInput from '../../../component/input/TextInput'
import connect from '../../../context/connect_to_server'
import UseAppContext from '../../../context/useAppContext'
import { GetUnique, isArray } from '../../../helper/essential'
import TableActivities from './PropTableConfig'

function PropTables() {
  const {
    list: data,
    user: { abbrv },
  } = React.useContext(UseAppContext)
  const [info, setInfo] = React.useState(null)
  const [reset, setReset] = React.useState(false)
  const [form, setForm] = React.useState({
    logger_type: '',
    receiver: '',
    receiver_names: [],
    message: '',
    unique_id: '',
    loading: false,
    errors: null,
    success: null,
  })

  React.useEffect(() => {
    ;(async function () {
      onChange('loading', true)
      const res = await connect.GET({
        urlRoute: 'user',
        data: { search: 'selection&skip=SHARED' },
      })

      if (res.success) {
        const np = isArray(res.data)
          ? GetUnique(res.data.map((d) => d.abbrv))
          : []
        setForm({ ...form, loading: false, receiver_names: np })
      } else
        setForm({
          ...form,
          loading: false,
          errors: 'No registered SITE-VERIFICATION-OFFICER found',
        })
    })()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])
  const {
    errors,
    loading,
    success,
    logger_type,
    receiver,
    message,
    receiver_names,
    unique_id,
  } = form
  const onChange = (name, value) => setForm({ ...form, [name]: value })

  const click = (d) => {
    setReset(true)
    setInfo(d)
  }

  const onSubmit = async (d) => {
    setForm({ ...form, loading: true })
    setReset(false)
    const res = await connect.POST({
      urlRoute: `order`,
      data: {
        logger_type,
        receiver,
        unique_id: unique_id || info.customer_id,
        sender: abbrv,
        message,
      },
    })

    if (res.success) {
      setForm({ ...form, success: res.data, loading: false })
    } else setForm({ ...form, loading: false, errors: res.error })
  }

  return (
    <>
      {data && <TableActivities row={data} onPress={click} />}
      {reset && (
        <OpenBox
          show={reset}
          title={'Assign this Property to An Individual'}
          cancel={() => setReset(false)}
          onPress={() => receiver && logger_type && message && onSubmit()}
          press_title={'Assign'}
        >
          <Divider />
          <p style={{ margin: '0.5rem' }}>Request for reset token</p>
          <SelectAutoWidth
            label="Task Type"
            handleInputChange={onChange}
            value={logger_type}
            name={'logger_type'}
            size={'100%'}
            items={['Site Verification', 'Meter Installation']}
          />

          <TextInput
            label="Reference ID"
            required={true}
            onChange={onChange}
            name="unique_id"
            value={unique_id || info.customer_id}
            disabled={true}
          />

          <SelectAutoWidth
            label="Task Recipient"
            handleInputChange={onChange}
            value={receiver}
            name={'receiver'}
            size={'100%'}
            items={receiver_names}
          />

          <TextInput
            label="Specific Task"
            required={true}
            onChange={onChange}
            name="message"
            value={message}
            input_type={'textarea'}
          />
        </OpenBox>
      )}
      {loading && <Loading open={loading} />}
      {errors && <ErrorSnackbar msg={errors} />}
      {success && <SuccessSnackbar msg={success} />}
    </>
  )
}

export default PropTables
