import React from 'react'
import WrappedInBars from '../../component/bars/WrappedInBars'
import UseAppContext from '../../context/useAppContext'
import TabActivities from './TabActivities'

function Logger({ Side }) {
  const { adjustMany } = React.useContext(UseAppContext)
  React.useEffect(() => {
    ;(function () {
      adjustMany({ active: 'logs' })
    })()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  return (
    <WrappedInBars UL={Side}>
      <TabActivities />
    </WrappedInBars>
  )
}

export default Logger
