import * as React from 'react'
import Box from '@mui/material/Box'
import TextField from '@mui/material/TextField'

export default function TextInput({
  required = false,
  name,
  onChange,
  label,
  value,
  input_type = 'text',
  disabled,
}) {
  const [focus, setFocus] = React.useState(false)
  const [error, setError] = React.useState(false)

  return (
    <Box
      component="form"
      sx={{
        '& > :not(style)': { m: 1, width: '25ch' },
      }}
      noValidate
      autoComplete="off"
    >
      <TextField
        id="outlined-basic"
        label={label}
        value={value}
        variant="outlined"
        onChange={(vx) => onChange && onChange(name, vx.target.value)}
        error={error}
        disabled={disabled}
        type={input_type}
        helperText={
          error
            ? label
              ? `${label} is required`
              : 'This field is required'
            : ''
        }
        style={{ width: '100%' }}
        onFocus={() => setFocus(true)}
        onBlur={() => focus && !value && required && setError(true)}
      />
    </Box>
  )
}
