import Paper from '@mui/material/Paper'
import Typography from '@mui/material/Typography'
import Box from '@mui/material/Box'
import { COLORS } from '../../../constants'
import { Button } from '@mui/material'

function GfWrap({
  value = 0,
  title = 'Unallocated Meters',
  onPress,
  disabled = true,
}) {
  return (
    <Box
      marginY={1}
      sx={{
        backgroundColor: COLORS.primary,
        width: '100%',
        height: '10.5rem',
        margin: '0 0.5rem',
      }}
    >
      <Typography
        component={'h6'}
        variant={'subtitle1'}
        marginLeft={0.5}
        padding={1.2}
        color={COLORS.white}
        textAlign={'center'}
        sx={{ borderBottom: `2px solid ${COLORS.bg}` }}
      >
        {title}
      </Typography>

      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          alignItems: 'center',
          padding: 0.5,
        }}
      >
        <Typography
          component={'h3'}
          variant={'h4'}
          sx={{ color: COLORS.bg, marginY: 1, paddingBottom: '0.5rem' }}
        >
          {value || 0}
        </Typography>
        <Button
          disabled={disabled}
          onClick={onPress}
          sx={{ backgroundColor: COLORS.light_primary, width: '90%' }}
        >
          Allocate
        </Button>
      </Box>
    </Box>
  )
}

const AllocationCards = ({ data, onPress }) => {
  const { singlePhase, md, threePhase, total, dt } = data || {}
  return (
    <Paper
      elevation={1}
      sx={{
        height: '12.5rem',
        marginY: 2,
        padding: 2,
        justifyContent: 'space-between',
        alignItems: 'center',
        display: 'flex',
      }}
    >
      <GfWrap onPress={onPress} value={total} />
      <GfWrap
        onPress={onPress}
        value={singlePhase}
        title={'1-Phases (Non-MD)'}
      />
      <GfWrap
        onPress={onPress}
        value={threePhase}
        title={'3-Phases (Non-MD)'}
      />
      <GfWrap onPress={onPress} value={md} title={'Maximum Demand'} />
      <GfWrap onPress={onPress} value={dt} title={'DT Meter'} />
    </Paper>
  )
}

export default AllocationCards
