import { Typography, Box } from '@mui/material'
import React from 'react'
import { BTN } from '../../../component/input/InputEssential'
import TextInput from '../../../component/input/TextInput'
import UseAppContext from '../../../context/useAppContext'

function ShareProfileFinish({ parent_form, submit, onPageChange }) {
  const { user } = React.useContext(UseAppContext)
  const { basic_data, locs_data, oth_info_data, coverage_data } =
    parent_form || {}
  const [form, setForm] = React.useState({ ...parent_form.finish_data })
  const { password } = form
  const onChange = (name, value) => setForm({ ...form, [name]: value })

  const data = {
    ...basic_data,
    ...locs_data,
    ...oth_info_data,
    ...coverage_data,
    password,
    nationality: locs_data.nationality || 'Nigeria',
    parent_user: user && user.email,
    shared_profile: true,
  }

  //   const change = (name, value) => _state({ ...parent_form, [name]: value })

  const onSubmit = async () => submit(data, true)

  return (
    <Box marginY={2}>
      <Typography variant="h5" component={'p'} marginTop={2} marginBottom={3}>
        Finish
      </Typography>

      {/* <Grid container spacing={2}> */}
      <INPUT
        onChange={onChange}
        name={'password'}
        value={password}
        label={'Password'}
        required={true}
      />
      {/* </Grid> */}

      <BTN
        onSubmit={onSubmit}
        disabled={!password}
        title={'Upload Data'}
        onCancel={() =>
          onPageChange('oth_info', true, true, 'finish_data', form)
        }
      />
    </Box>
  )
}

function INPUT({
  onChange,
  name,
  value,
  required,
  span = 4,
  label,
  input_type,
}) {
  return (
    // <Grid item xs={span}>
    <TextInput
      onChange={onChange}
      name={name}
      value={value}
      required={required}
      label={label}
      input_type={input_type}
    />
    // </Grid>
  )
}

export default ShareProfileFinish
