import { Box, Grid, Typography } from '@mui/material'
import * as React from 'react'
import { useNavigate } from 'react-router-dom'
import Loading from '../../../component/alerts_boxes/Loading'
import { ErrorSnackbar } from '../../../component/alerts_boxes/SnackBar'
import WrappedInBars from '../../../component/bars/WrappedInBars'
import { BTN, CHL } from '../../../component/input/InputEssential'
import SelectAutoWidth from '../../../component/input/SelectComponent'
import TextInput from '../../../component/input/TextInput'
import connect from '../../../context/connect_to_server'
import UseAppContext from '../../../context/useAppContext'
import MPSide from '../MPSide'

const AllocationPage = () => {
  const { adjustMany, store_items, user } = React.useContext(UseAppContext)
  const { cartonID, pop, _id } = store_items || {}
  const { user_key } = user || {}

  const navigate = useNavigate()

  const [form, setForm] = React.useState({
    allocateAll: true,
    owner: '',
    opt: [],
    currentStore: '',
    piece: '',
    ed: false,
    recipient: '',
    destinationStore: '',
    loading: false,
    errors: null,
  })

  React.useEffect(() => {
    ;(async function () {
      if (!store_items) return navigate(`/${user_key}/dashboard`)
      adjustMany({ active: 'store' })
      const res = await connect.GET({
        params: `search=selection&skip=ALLOCATE`,
        urlRoute: 'user',
      })
      if (res.success) {
        const np = [...new Set(res.data.map((a) => a.abbrv))]
        setForm({ ...form, opt: np })
      } else
        setForm({
          ...form,
          errors: 'No registered DISCO found',
          loading: false,
        })
    })()

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const { allocateAll, piece, owner, recipient, destinationStore } = form
  const onChange = (name, value) => setForm({ ...form, [name]: value })

  const submit = async () => {
    setForm({ ...form, loading: true, errors: null })
    const res = await connect.PUT({
      urlRoute: `map/${cartonID}`,
      params: `limit=${allocateAll ? pop : piece}&search=ALLOCATE`,
      data: {
        allocatedTo: owner,
        piece: allocateAll ? pop : piece,
        allocationStatus: 'Allocated',
        destinationStore,
      },
    })

    if (res.success) {
      onChange('loading', false)
      navigate(`/${user_key}/store`)
    } else setForm({ ...form, loading: false, errors: res.error })
  }

  return (
    <WrappedInBars UL={MPSide}>
      <Box marginY={10}>
        <Typography variant="h4" component={'h6'} marginBottom={1}>
          Allocation by Carton Ref: {cartonID || ''}
        </Typography>

        <Typography variant="body1" component={'p'} marginBottom={1}>
          Total number in this category: {pop || 0} Meter(s)
        </Typography>

        <WR>
          <INPUT
            label={'Select Recipient'}
            onChange={onChange}
            name={'recipient'}
            value={recipient}
            items={['DISCO']}
          />

          <INPUT
            onChange={onChange}
            name={'owner'}
            value={owner}
            items={form.opt}
            label={'Select Recipient Name'}
          />
        </WR>

        <WR>
          <INPUT
            label={'Current Store'}
            onChange={onChange}
            name={'currentStore'}
            value={_id}
            disable={true}
          />

          <INPUT
            onChange={onChange}
            name={'destinationStore'}
            value={destinationStore}
            label={'Select Destination Store'}
          />
        </WR>

        <WR>
          <INPUT
            label={'Allocate Pieces from Carton'}
            onChange={onChange}
            name={'piece'}
            value={piece}
            disable={allocateAll && true}
          />
        </WR>

        <CHL
          label={'Allocate all ' + pop + ' items(s) '}
          handleChange={onChange}
          checked={allocateAll}
          toggle={allocateAll}
          name={'allocateAll'}
          disabled={piece && true}
        />

        {form.loading && <Loading open={form.loading} />}
        {form.errors && <ErrorSnackbar msg={form.errors} />}

        <BTN
          title="Send"
          onSubmit={submit}
          disabled={!owner || !destinationStore || (!allocateAll && !piece)}
        />
      </Box>
    </WrappedInBars>
  )
}

function INPUT({
  onChange,
  name,
  value,
  required,
  span = 4,
  label,
  input_type,
  items,
  disable,
}) {
  return (
    <Grid item xs={span}>
      {items ? (
        <SelectAutoWidth
          handleInputChange={onChange}
          name={name}
          value={value}
          label={label}
          items={items}
          size={'100%'}
        />
      ) : (
        <TextInput
          onChange={onChange}
          name={name}
          value={value}
          required={required}
          label={label}
          input_type={input_type}
          disabled={disable}
        />
      )}
    </Grid>
  )
}

function WR({ sp = 2, children }) {
  return (
    <Grid container spacing={sp}>
      {children}
    </Grid>
  )
}

export default AllocationPage
