import Box from '@mui/material/Box'
import IconButton from '@mui/material/IconButton'
import Badge from '@mui/material/Badge'
import AccountCircle from '@mui/icons-material/AccountCircle'
import MailIcon from '@mui/icons-material/Mail'
import NotificationsIcon from '@mui/icons-material/Notifications'
import MoreIcon from '@mui/icons-material/MoreVert'
import React from 'react'
import UseAppContext from '../../../context/useAppContext'
import { useNavigate } from 'react-router-dom'
import connect from '../../../context/connect_to_server'

function SideIconNots({
  handleMobileMenuOpen,
  handleProfileMenuOpen,
  mobileMenuId,
  menuId,
}) {
  const { nots, ms, user, adjustMany } = React.useContext(UseAppContext)
  const { user_key, abbrv } = user || {}
  const navigate = useNavigate()
  const [data, setData] = React.useState(null)

  React.useEffect(() => {
    ;(async function () {
      const res = nots
        ? nots
        : await connect.GET({
            urlRoute: 'order',
            data: { receiver: abbrv, search: 'RECEIVER' },
          })
      if (res.success && !nots) {
        setData(res.data.length)
        adjustMany({ nots: res.data.length })
      } else setData(nots)
    })()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])
  return (
    <>
      <Box sx={{ display: { xs: 'none', md: 'flex' } }}>
        <IconButton
          size="large"
          aria-label={ms ? `show ${ms} new Logs` : ''}
          color="inherit"
          onClick={() => navigate(`/${user_key}/logs`)}
        >
          <Badge badgeContent={ms || 0} color="error">
            <MailIcon />
          </Badge>
        </IconButton>
        <IconButton
          size="large"
          aria-label={data ? `show ${data} new notifications` : ''}
          color="inherit"
          onClick={() => navigate(`/${user_key}/logs`)}
        >
          <Badge badgeContent={data || 0} color="error">
            <NotificationsIcon />
          </Badge>
        </IconButton>
        <IconButton
          size="large"
          edge="end"
          aria-label="account of current user"
          aria-controls={menuId}
          aria-haspopup="true"
          onClick={handleProfileMenuOpen}
          color="inherit"
        >
          <AccountCircle />
        </IconButton>
      </Box>
      <Box sx={{ display: { xs: 'flex', md: 'none' } }}>
        <IconButton
          size="large"
          aria-label="show more"
          aria-controls={mobileMenuId}
          aria-haspopup="true"
          onClick={handleMobileMenuOpen}
          color="inherit"
        >
          <MoreIcon />
        </IconButton>
      </Box>
    </>
  )
}

export default SideIconNots
