import React from 'react'
import { styled, alpha } from '@mui/material/styles'
import InputBase from '@mui/material/InputBase'
import SearchIcon from '@mui/icons-material/Search'
import Menu from '@mui/icons-material/MenuOpen'
import Find from '@mui/icons-material/FindInPage'
import Box from '@mui/material/Box'
import UseAppContext from '../../../context/useAppContext'
import OpenBox from '../../alerts_boxes/OpenBox'
import SelectAutoWidth from '../../input/SelectComponent'
import { Divider } from '@mui/material'
import { ErrorSnackbar } from '../../alerts_boxes/SnackBar'
import connect from '../../../context/connect_to_server'
import Loading from '../../alerts_boxes/Loading'
import { useNavigate } from 'react-router-dom'
import { date_config } from '../../../helper/essential'
import TextInput from '../../input/TextInput'

const Search = styled('div')(({ theme }) => ({
  position: 'relative',
  display: 'flex',
  alignItems: 'center',
  paddingRight: '0.5rem',
  justifyContent: 'space-between',
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  '&:hover': {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginRight: theme.spacing(2),
  marginLeft: 0,
  width: '100%',
  [theme.breakpoints.up('sm')]: {
    marginLeft: theme.spacing(3),
    width: 'auto',
  },
}))

const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}))

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: '20ch',
    },
  },
}))

function SearchBar({ onPress }) {
  const { adjustMany, user } = React.useContext(UseAppContext)
  const { user_key, map, disco } = user || {}
  const navigate = useNavigate()

  const et = date_config().fdate

  const [modal, setModal] = React.useState(false)
  const [text, setText] = React.useState('')
  const [focus, setFocus] = React.useState(false)
  const [form, setForm] = React.useState({
    profile: '',
    loading: false,
    uploaded_by: '',
    disco_acknowledgement_by: '',
    disco_allocation_to: '',
    allocation_date: et,
    acknowledged_date: et,
    installation_date: et,
    meter_series: '',
    errors: null,
  })

  const {
    disco_acknowledgement_by,
    uploaded_by,
    disco_allocation_to,
    acknowledged_date,
    allocation_date,
    installation_date,
    meter_series,
    loading,
    errors,
  } = form
  const onChange = (name, value) => setForm({ ...form, [name]: value })

  const onSubmit = async () => {
    if (text) {
      setForm({ ...form, errors: null, loading: true })
      const res = await connect.GET({ urlRoute: `disco/${text}` })
      if (res.success) {
        adjustMany({ meter_info: [res.data] })
        setForm({ ...form, loading: false })
        navigate(`/${user_key}/metering-result`)
      } else {
        setForm({ ...form, errors: res.error, loading: false })
      }
    }
  }

  const querie = async () => {
    setForm({ ...form, errors: null, loading: true })
    setModal(false)
    const res = await connect.GET({
      urlRoute: `disco`,
      data: {
        disco_acknowledgement_by,
        disco_allocation_to,
        allocation_date,
        meter_series,
        uploaded_by,
        acknowledged_date,
      },
    })
    if (res.success) {
      adjustMany({ meter_info: res.data })
      setForm({ ...form, loading: false })
      navigate(`/${user_key}/metering-result`)
      setModal(false)
    } else {
      setForm({ ...form, errors: res.error, loading: false })
      setModal(false)
    }
  }

  return (
    <Search>
      <SearchIconWrapper>
        <SearchIcon />
      </SearchIconWrapper>
      <StyledInputBase
        placeholder="Search…"
        inputProps={{ 'aria-label': 'search' }}
        onChange={(e) => setText(e.target.value)}
        onFocus={() => setFocus(true)}
        onBlur={() => {
          if (!text) setFocus(false)
        }}
      />
      {!focus ? (
        <Box
          marginLeft={5}
          onClick={() => setModal(true)}
          sx={{
            cursor: 'pointer',
            justifyContent: 'center',
            alignItems: 'center',
            display: 'flex',
          }}
        >
          <Menu />
        </Box>
      ) : (
        <Box
          marginLeft={5}
          onClick={() => (onPress ? onPress(text) : onSubmit())}
          sx={{
            cursor: 'pointer',
            justifyContent: 'center',
            alignItems: 'center',
            display: 'flex',
          }}
        >
          <Find />
        </Box>
      )}

      {modal && (
        <OpenBox
          show={modal}
          title={'Customize Search'}
          cancel={() => setModal(false)}
          onPress={querie}
          press_title={'Search'}
        >
          <Divider />
          <p style={{ margin: '0.5rem' }}>
            Allow user to share profile with others
          </p>
          <SelectAutoWidth
            handleInputChange={onChange}
            name={'uploaded_by'}
            value={uploaded_by}
            label={'Manufacturer Name'}
            items={map}
            size={'100%'}
          />

          <SelectAutoWidth
            handleInputChange={onChange}
            name={'disco_acknowledgement_by'}
            value={disco_acknowledgement_by}
            label={'Meter Allocated To (DISCO)'}
            items={disco}
            size={'100%'}
          />

          <INPUT
            onChange={onChange}
            name={'allocation_date'}
            value={allocation_date}
            required={true}
            label={'Allocation Date'}
            input_type={'date'}
          />

          <INPUT
            onChange={onChange}
            name={'acknowledged_date'}
            value={acknowledged_date}
            required={true}
            label={'Disco Acknowledged Date'}
            input_type={'date'}
          />

          <INPUT
            onChange={onChange}
            name={'installation_date'}
            value={installation_date}
            required={true}
            label={'Meter Installation Date'}
            input_type={'date'}
          />
        </OpenBox>
      )}

      {errors && <ErrorSnackbar msg={errors} />}
      {loading && <Loading open={loading} />}
    </Search>
  )
}

function INPUT({
  onChange,
  name,
  value,
  required,
  span = 4,
  label,
  input_type,
}) {
  return (
    <TextInput
      onChange={onChange}
      name={name}
      value={value}
      required={required}
      label={label}
      input_type={input_type}
    />
  )
}

export default SearchBar
