import { Grid, Typography, Box } from '@mui/material'
import React from 'react'
import { useNavigate } from 'react-router-dom'
import { BTN } from '../../../../component/input/InputEssential'
import TextInput from '../../../../component/input/TextInput'
import connect from '../../../../context/connect_to_server'

function AddUserFinish({ parent_form, _state, onPageChange }) {
  const navigate = useNavigate()
  const { basic_data, locs_data, oth_info_data, coverage_data } =
    parent_form || {}
  const [form, setForm] = React.useState({ ...parent_form.finish_data })
  const { password } = form
  const onChange = (name, value) => setForm({ ...form, [name]: value })

  const data = {
    ...basic_data,
    ...locs_data,
    ...oth_info_data,
    ...coverage_data,
    password,
    nationality: locs_data.nationality || 'Nigeria',
    password1: password,
  }

  //   const change = (name, value) => _state({ ...parent_form, [name]: value })

  const onSubmit = async () => {
    _state({ ...parent_form, errors: null, success: null, loading: true })
    const res = await connect.POST({ urlRoute: 'user', data })
    if (res.success) {
      navigate('/')
      _state({
        ...parent_form,
        loading: false,
        success: 'User created successfully...',
      })
    } else _state({ ...parent_form, loading: false, errors: res.error })
  }

  return (
    <Box marginY={2}>
      <Typography variant="h5" component={'p'} marginTop={2} marginBottom={3}>
        Finish
      </Typography>

      <Grid container spacing={2}>
        <INPUT
          onChange={onChange}
          name={'password'}
          value={password}
          label={'Password'}
          required={true}
        />
      </Grid>

      <BTN
        onSubmit={onSubmit}
        disabled={!password}
        title={'Upload Data'}
        onCancel={() =>
          onPageChange('coverage', true, true, 'finish_data', form)
        }
      />
    </Box>
  )
}

function INPUT({
  onChange,
  name,
  value,
  required,
  span = 4,
  label,
  input_type,
}) {
  return (
    <Grid item xs={span}>
      <TextInput
        onChange={onChange}
        name={name}
        value={value}
        required={required}
        label={label}
        input_type={input_type}
      />
    </Grid>
  )
}

export default AddUserFinish
