import TableCell from '@mui/material/TableCell'
import TableRow from '@mui/material/TableRow'

import { Box, TablePagination } from '@mui/material'
import React from 'react'
// import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye'
import TableTemplate from '../../component/tables/simple_table/TableTemplate'

const hdr = [
  'Log Title',
  'Log Unique ID',
  'Sender',
  'Receiver',
  'Message',
  'Timestamp',
]

const bd = [
  {
    loggerType: 'Logs Title',
    sender: '39e',
    receiver: '38',
    loggerID: '3',
    message: '892',
    createdAt: 'Now',
  },
]

export default function TableNots({ header = hdr, row = bd, onPress }) {
  const rows = row && Object.keys(row).toString().startsWith(0) ? row : [row]
  const [page, setPage] = React.useState(0)
  const [rowsPerPage, setRowsPerPage] = React.useState(5)

  const handleChangePage = (event, newPage) => {
    setPage(newPage)
  }

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10))
    setPage(0)
  }

  return (
    <Box sx={{ width: '100%' }}>
      <TableTemplate header={header}>
        {rows
          .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
          .map((d, i) => (
            <TableRow
              key={i}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              {/* cartonID, meterOwner, storeID, destinationStore,
                        phase, manufacturerName, defaultUnit, pop */}
              <TableCell> {d.loggerType} </TableCell>
              <TableCell> {d.loggerID} </TableCell>
              <TableCell> {d.sender} </TableCell>
              <TableCell> {d.receiver} </TableCell>
              <TableCell> {d.message} </TableCell>
              <TableCell> {d.createdAt} </TableCell>
              {/* <TableCell>
                <Tooltip title="Requires Attention">
                  <IconButton onClick={() => onPress(d)}>
                    <RemoveRedEyeIcon />
                  </IconButton>
                </Tooltip>
              </TableCell> */}
            </TableRow>
          ))}
      </TableTemplate>
      <TablePagination
        rowsPerPageOptions={[5, 10, 15, 20]}
        component="div"
        count={rows.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={handleChangePage}
        onRowsPerPageChange={handleChangeRowsPerPage}
      />
    </Box>
  )
}
