import { Typography } from '@mui/material'
import Box from '@mui/material/Box'
import React from 'react'
import WrappedInBars from '../../../component/bars/WrappedInBars'
import UseAppContext from '../../../context/useAppContext'
import AdsSide from '../AdsSide'
import AdminCard from './dashboard/AdminCard'
import AdminTable from './dashboard/AdminTable'
import { useNavigate } from 'react-router-dom'
import connect from '../../../context/connect_to_server'
import Loading from '../../../component/alerts_boxes/Loading'

function AdminDashoard() {
  const { adjustMany, user } = React.useContext(UseAppContext)
  const { user_key } = user || {}
  const navigate = useNavigate()
  const [data, setData] = React.useState(null)
  const [loading, setLoading] = React.useState(false)

  React.useEffect(() => {
    ;(async function () {
      adjustMany({ active: 'dashboard' })
      setLoading(true)
      const res = await connect.GET({ urlRoute: 'user' })
      if (res.success) {
        setData(res.data)
        setLoading(false)
      } else {
        setLoading(false)
      }
    })()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  return (
    <WrappedInBars UL={AdsSide}>
      <Box marginY={10}>
        <Typography variant="h4" component={'h6'} marginBottom={1}>
          Registered Profiles
        </Typography>

        {data && <AdminCard data={data} />}
        <Typography variant="h6" component={'h6'} marginY={2}>
          User Details
        </Typography>
        {data && (
          <AdminTable
            row={data}
            onPress={(da) => {
              adjustMany({ view_user: da })
              return navigate(`${user_key}/view-user`)
            }}
          />
        )}
        {loading && <Loading open={loading} />}
      </Box>
    </WrappedInBars>
  )
}

export default AdminDashoard
