import { Person, ArrowBack } from '@mui/icons-material'
import { Typography, Paper, Box, IconButton, Button } from '@mui/material'
import React from 'react'
import { COLORS } from '../../../constants'
import UseAppContext from '../../../context/useAppContext'
import { NumberRefactor, numberWithCommas } from '../../../helper/essential'

export const TopBox = ({
  title = 'Total Users',
  value = 0,
  Icon = Person,
  bg = 'rgb(178, 180, 183,0.8)',
  hg = 120,
}) => {
  return (
    <Paper
      elevation={12}
      style={{
        height: hg,
        backgroundColor: bg,
        margin: 1,
        borderRadius: '0.5rem',
        width: '100%',
      }}
    >
      <Box
        style={{
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          padding: '1rem 1rem 0.2rem',
          width: '100%',
        }}
      >
        <Icon
          sx={{
            fontSize: '2.9rem',
            padding: 0.2,
            backgroundColor: COLORS.white,
            borderRadius: '0.5rem',
            color: COLORS.primary,
          }}
        />

        <Typography variant={'h3'} component={'h1'}>
          {NumberRefactor(value)}
        </Typography>
      </Box>
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          padding: '0.2rem 1rem',
          width: '100%',
        }}
      >
        <Typography variant="h6" component={'h6'}>
          {title}
        </Typography>
      </Box>
    </Paper>
  )
}

export const TopBxOutline = ({
  title = 'Total Users',
  value = 0,
  Icon,
  bg = 'transparent',
  hg = 80,
}) => {
  return (
    <Paper
      elevation={12}
      style={{
        height: hg,
        display: 'flex',
        alignItems: 'center',
        backgroundColor: bg,
        margin: 1,
        borderRadius: '0.5rem',
        width: '100%',
        border: `solid 2px ${COLORS.white}`,
      }}
    >
      <Box sx={{ padding: 1, color: COLORS.white }}>
        <Typography variant={'h4'} component={'h6'}>
          {NumberRefactor(value)}
        </Typography>
        <Typography
          variant="body1"
          component={'h6'}
          sx={{ fontSize: '0.9rem' }}
        >
          {title}
        </Typography>
      </Box>
    </Paper>
  )
}

export const TpTxt = ({ value = 40, text = '1-Phase' }) => {
  return (
    <Box
      sx={{
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        color: COLORS.white,
        margin: '0 1rem',
      }}
    >
      <Typography
        variant="subtitle1"
        component={'h6'}
        sx={{ textTransform: 'uppercase', marginRight: 1, color: 'yellow' }}
      >
        {text}:
      </Typography>
      <Typography component={'h6'} variant={'h5'} sx={{ marginLeft: 1 }}>
        {numberWithCommas(value)}
      </Typography>
    </Box>
  )
}

export const PhasePane = ({
  title = '1-PHASE',
  sent = 0,
  received = 0,
  value = 40,
  stxt = 'SENT',
  ttxt = 'RECEIVED',
}) => {
  return (
    <Paper
      sx={{
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        flexDirection: 'column',
        margin: '0 1rem',
        padding: 1,
      }}
      elevation={3}
    >
      <Box
        sx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'flex-start',
        }}
      >
        <h6
          style={{ fontSize: '0.7rem', fontWeight: 'normal', marginRight: 10 }}
        >
          {title}
        </h6>
        <h5 style={{ color: COLORS.secondary }}>{NumberRefactor(value)}</h5>
      </Box>
      <Box
        sx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
        }}
      >
        <Box
          sx={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'flex-start',
          }}
        >
          <h6
            style={{
              fontSize: '0.7rem',
              fontWeight: 'normal',
              marginRight: 10,
            }}
          >
            {stxt}:
          </h6>
          <h5 style={{ color: COLORS.teal }}>{NumberRefactor(sent)}</h5>
        </Box>

        <Box
          sx={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'flex-start',
            marginLeft: 2,
          }}
        >
          <h6
            style={{
              fontSize: '0.7rem',
              fontWeight: 'normal',
              marginRight: 10,
            }}
          >
            {ttxt}:
          </h6>
          <h5 style={{ color: 'orange' }}>{NumberRefactor(received)}</h5>
        </Box>
      </Box>
    </Paper>
  )
}

export const GoPreview = ({ Icon = ArrowBack, Press, disable = false }) => {
  return (
    <IconButton
      sx={{ marginLeft: 0.5, backgroundColor: COLORS.light_primary }}
      onClick={Press}
      disabled={disable}
    >
      <Icon />
    </IconButton>
  )
}

export const DSK = ({ title = 'Button', Press, name }) => {
  const { dsk: active } = React.useContext(UseAppContext)
  return (
    <Button
      variant="contained"
      sx={{
        border: `1px solid ${COLORS.secondary}`,
        backgroundColor: name === active ? COLORS.primary : 'transparent',
        color: name === active ? COLORS.white : COLORS.primary,
        width: '100%',
        margin: '0.2rem 0',
      }}
      onClick={() => Press && Press(title)}
    >
      {title}
    </Button>
  )
}
