import React from 'react'
import Loading from '../../component/alerts_boxes/Loading'
import { ErrorSnackbar } from '../../component/alerts_boxes/SnackBar'
import connect from '../../context/connect_to_server'
import UseAppContext from '../../context/useAppContext'
import TableNots from './TableNots'

function NotsTable() {
  const { user, adjustMany } = React.useContext(UseAppContext)
  const { abbrv, fullName } = user || {}
  const [data, setData] = React.useState(null)
  const [loading, setLoading] = React.useState(false)
  const [errors, setErrors] = React.useState(null)

  React.useEffect(() => {
    ;(async function () {
      setLoading(true)
      const res = await connect.GET({
        urlRoute: 'order',
        data: { receiver: abbrv, search: 'RECEIVER' },
      })
      if (res.success) {
        setLoading(false)
        setData(res.data)
        adjustMany({ nots: res.data.length })
      } else {
        setLoading(false)
        setErrors(res.error)
      }
    })()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])
  return (
    <>
      {data && <TableNots row={data} />}
      {loading && <Loading open={loading} />}
      {errors && (
        <ErrorSnackbar msg={`No Log(s) found for User: ${fullName}`} />
      )}
    </>
  )
}

export default NotsTable
