import { GasMeter, HourglassBottom, PhoneAndroid } from '@mui/icons-material'
import { Box, Typography } from '@mui/material'
import React from 'react'
import { Link } from 'react-router-dom'
import { COLORS } from '../../../constants'
import UseAppContext from '../../../context/useAppContext'
import { PhasePane, TopBox, TopBxOutline } from './TopMidBox'

function TopBack({ data = {} }) {
  const { user } = React.useContext(UseAppContext)
  const { abbrv, role } = user || {}
  const nd = role === 'DISCO'
  const { header_stats, phase } = data || {}
  const { single, three } = phase || {}
  const {
    map_total_uploads,
    total_meter,
    disco_allocations,
    total_meter_applicant,
    metered_applicant,
    unAllocatedMeterCount,
    // installation_count,
    replacementRegistered,
    replacementTreated,
    // pendingReplacement,
  } = header_stats || {}

  return (
    <Box
      sx={{
        backgroundColor: COLORS.primary,
        // height: '50vh',
        zIndex: -1,
        paddingLeft: '11.5rem',
      }}
    >
      <Box
        sx={{
          padding: 1,
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          gap: '1rem',
        }}
      >
        <TopBox
          value={total_meter || 0}
          title="Project Goal / Target"
          Icon={HourglassBottom}
        />
        <TopBox
          bg="rgb(213, 149, 241,0.8)"
          value={map_total_uploads}
          title={nd ? 'Meter Received' : 'Meter Supplied'}
          Icon={GasMeter}
        />
        <TopBox value={single || 0} title="Total 1-Phase" Icon={PhoneAndroid} />
        <TopBox
          bg="rgb(213, 149, 241,0.8)"
          value={three || 0}
          title="Total 3-Phase"
          Icon={PhoneAndroid}
        />
      </Box>

      <Box
        sx={{
          padding: 1,
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          gap: '1rem',
        }}
      >
        <TopBxOutline
          value={nd ? total_meter_applicant : disco_allocations}
          title={nd ? 'Meter Request Received' : 'Meters Received By DISCO(s)'}
        />
        <TopBxOutline
          value={nd ? disco_allocations : unAllocatedMeterCount}
          title={nd ? 'Allocation to Property' : 'Pending Allocation'}
        />
        <TopBxOutline
          value={nd ? metered_applicant : replacementRegistered}
          title={nd ? 'Metered Property' : 'Meter Replacement Request'}
        />
        <TopBxOutline
          value={nd ? replacementRegistered : replacementTreated}
          title={
            nd
              ? 'Replacement Request (Untreated) '
              : 'Meter Replacement Treated'
          }
        />
      </Box>

      <Box
        sx={{
          padding: 0.5,
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          width: '75%',
          margin: 0.5,
          paddingBottom: 1,
        }}
      >
        <Typography
          sx={{ color: COLORS.white }}
          component={'h5'}
          variant={'h6'}
        >
          <Link to={`/`} style={{ color: COLORS.white }}>
            Home
          </Link>{' '}
          / {abbrv} Summary View
        </Typography>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
          }}
        >
          <PhasePane
            title="TOTAL:"
            value={total_meter}
            sent={single}
            received={three}
            stxt={'1-PHASE'}
            ttxt={'3-PHASE'}
          />
        </Box>
      </Box>
    </Box>
  )
}

export default TopBack
