import Typography from '@mui/material/Typography'
import Box from '@mui/material/Box'
import React from 'react'
import Loading from '../../component/alerts_boxes/Loading'
import {
  ErrorSnackbar,
  SuccessSnackbar,
} from '../../component/alerts_boxes/SnackBar'
import { generateString } from '../admin/pages/adduser/PasswordGen'
import SHBasicAddress from './shuser/SHBasicAddress'
import SHBasicInformation from './shuser/SHBasicInformation'
import SHOtherInformation from './shuser/SHOtherInformation'
import ShareProfileFinish from './shuser/ShareProfileFinish'
import { CHL } from '../../component/input/InputEssential'

function SHAddUser({ submit }) {
  const pass = generateString()
  const [form, setForm] = React.useState({
    counter: 0,
    basic: true,
    locs: false,
    oth_info: false,
    coverage: false,
    individual: true,
    company: false,
    finish: false,
    loading: false,
    errors: null,
    success: null,
    basic_data: {
      fname: '',
      lname: '',
      email: '',
      telephone: '',
      identity_id: '',
      identity_type: '',
    },
    locs_data: {
      street_number: '',
      street_name: '',
      city: '',
      province: '',
      lat: '',
      lng: '',
    },
    oth_info_data: {
      role: '',
      abbrv: '',
      right_to_share_profile: false,
    },
    coverage_data: {
      franchise_states: [],
    },
    finish_data: {
      password: pass,
    },
  })

  const handleChange = (name, value) =>
    setForm({ ...form, individual: false, company: false, [name]: value })

  const onPageChange = (name, value, pre, th, vl) =>
    setForm({
      ...form,
      locs: false,
      basic: false,
      oth_info: false,
      coverage: false,
      finish: false,
      counter: pre ? counter - 1 : counter + 1,
      [name]: value,
      [th]: vl,
    })

  const {
    individual,
    company,
    counter,
    locs,
    basic,
    oth_info,
    finish,
    loading,
    errors,
    success,
  } = form

  return (
    <Box paddingY={1}>
      <Typography component={'h2'} variant={'h5'} marginBottom={1}>
        Add a User
      </Typography>

      {basic && (
        <Typography component={'p'} variant={'body1'} marginTop={2}>
          To get started, fill out some basic information about who you're
          sharing your profile with.
        </Typography>
      )}

      {basic && (
        <Box>
          <CHL
            label={'Company / Corporate Body'}
            handleChange={handleChange}
            checked={company}
            name={'company'}
            disabled={true}
          />
          <CHL
            label={'Individaul'}
            handleChange={handleChange}
            checked={individual}
            name={'individual'}
          />
        </Box>
      )}

      {basic && (
        <SHBasicInformation parent_form={form} onPageChange={onPageChange} />
      )}

      {locs && (
        <SHBasicAddress parent_form={form} onPageChange={onPageChange} />
      )}

      {oth_info && (
        <SHOtherInformation
          parent_form={form}
          onPageChange={onPageChange}
          handleChange={handleChange}
        />
      )}

      {finish && (
        <ShareProfileFinish
          parent_form={form}
          onPageChange={onPageChange}
          submit={submit}
        />
      )}

      {loading && <Loading open={loading} />}
      {errors && <ErrorSnackbar msg={errors} />}
      {success && <SuccessSnackbar msg={success} />}
    </Box>
  )
}

export default SHAddUser
