import React from 'react'
import connect from '../context/connect_to_server'
import UseAppContext from '../context/useAppContext'
import Ads from '../layout/admin/Ads'
import Login from './Login'
import Blank from './Blank'
import Cbs from '../layout/cbs/Cbs'
import Mps from '../layout/map/Mps'
import Ds from '../layout/disco/Ds'

export default function PageLoadRender() {
  const { isAuthenticated, user, adjustMany } = React.useContext(UseAppContext)
  if (!isAuthenticated) return <Login />
  const { role } = user || {}

  const Press = async () => {
    adjustMany({ isAuthenticated: false, user: null })
    await connect.clearAll()
  }

  // const TAG = ['CBN', 'NERC']

  if (role === 'ADMIN') return <Ads />
  else if (role === 'CBN') return <Cbs />
  else if (role === 'MAP') return <Mps />
  else if (role === 'DISCO') return <Ds />
  return <Blank Press={Press} />
}
