import TableCell from '@mui/material/TableCell'
import TableRow from '@mui/material/TableRow'

import { Box, IconButton, TablePagination, Tooltip } from '@mui/material'
import React from 'react'
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye'
import TableTemplate from '../../../component/tables/simple_table/TableTemplate'

const hdr = ['#', 'Store Owner', 'Store Name', 'Total Meters', 'Action']

const bd = [
  {
    total: 'cn@gmail.com',
    map: 'ADMIN',
    _id: 'Active',
  },
]
// slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)map((row, index).
export default function StoreTable({ header = hdr, row = bd, onPress }) {
  const rows = row && Object.keys(row).toString().startsWith(0) ? row : [row]
  const [page, setPage] = React.useState(0)
  const [rowsPerPage, setRowsPerPage] = React.useState(5)

  const handleChangePage = (event, newPage) => {
    setPage(newPage)
  }

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10))
    setPage(0)
  }

  return (
    <Box sx={{ width: '100%' }}>
      <TableTemplate header={header}>
        {rows
          .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
          .map((d, i) => (
            <TableRow
              key={i}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell> {i + 1} </TableCell>
              <TableCell> {d.map} </TableCell>
              <TableCell> {d._id} </TableCell>
              <TableCell> {d.total} </TableCell>
              <TableCell>
                <Tooltip title="View Store Details">
                  <IconButton onClick={() => onPress(d)}>
                    <RemoveRedEyeIcon />
                  </IconButton>
                </Tooltip>
              </TableCell>
            </TableRow>
          ))}
      </TableTemplate>
      <TablePagination
        rowsPerPageOptions={[5, 10, 15, 20]}
        component="div"
        count={rows.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={handleChangePage}
        onRowsPerPageChange={handleChangeRowsPerPage}
      />
    </Box>
  )
}
