import Reset from '@mui/icons-material/Restore'
import Block from '@mui/icons-material/Block'
// import Suspend from '@mui/icons-material/PersonAddDisabled'
import { Avatar, Button, Typography, Divider } from '@mui/material'
import Loading from '../../../../../component/alerts_boxes/Loading'
import OpenBox from '../../../../../component/alerts_boxes/OpenBox'
import {
  ErrorSnackbar,
  SuccessSnackbar,
} from '../../../../../component/alerts_boxes/SnackBar'
import SelectAutoWidth from '../../../../../component/input/SelectComponent'

import Box from '@mui/material/Box'
import React from 'react'
import connect from '../../../../../context/connect_to_server'

function HeaderView({ data }) {
  const { fullName, email } = data || {}
  const ab = fullName ? fullName.substr(0, 3) : ''
  const [reset, setReset] = React.useState(false)
  const [disable, setDisable] = React.useState('')
  const [showable, setShowable] = React.useState(false)

  const [form, setForm] = React.useState({
    loading: false,
    success: null,
    errors: null,
  })
  const { loading, success, errors } = form

  const submit = async (data = {}) => {
    setForm({ ...form, loading: true, success: null, errors: null })
    const res = await connect.PUT({ urlRoute: `user/${email}`, data })
    if (res.success) {
      setForm({
        ...form,
        loading: false,
        success: 'Profile updated successfully',
      })
    } else setForm({ ...form, loading: false, errors: res.error })
  }

  return (
    <Box
      style={{
        display: 'flex',
        justifyContent: 'flex-start',
        alignItems: 'center',
      }}
      marginY={2}
    >
      <Avatar
        sx={{
          width: 90,
          height: 90,
          marginRight: 5,
          fontSize: 40,
          textTransform: 'uppercase',
        }}
      >
        {ab}
      </Avatar>
      <Box>
        <Typography component={'h6'} variant={'h4'}>
          {fullName}
        </Typography>
        <Box
          style={{
            display: 'flex',
            justifyContent: 'flex-start',
            alignItems: 'center',
          }}
        >
          <Button startIcon={<Reset />} onClick={() => setReset(true)}>
            Reset Password
          </Button>
          <Button
            startIcon={<Block />}
            sx={{ marginLeft: 1 }}
            onClick={() => setShowable(true)}
          >
            Block Sign-in
          </Button>
          {/* <Button
            startIcon={<Suspend />}
            sx={{ marginLeft: 1 }}
            disabled={!shared_profile}
          >
            View Share Profile
          </Button> */}
        </Box>
      </Box>

      {showable && (
        <OpenBox
          show={showable}
          title={'Disable User Profile'}
          cancel={() => setShowable(false)}
          onPress={async () => {
            submit({
              is_disabled: disable === 'Disable' ? true : false,
            })
            setDisable('')
            setShowable(false)
          }}
          press_title={'Activate'}
        >
          <Divider />
          <p style={{ margin: '0.5rem' }}>
            Are you Sure you want to Disable {fullName}
          </p>
          <SelectAutoWidth
            handleInputChange={(name, value) => setDisable(value)}
            name={'disable'}
            value={disable}
            label={'User Availability'}
            items={['Disable', 'Activate']}
            size={'100%'}
          />
        </OpenBox>
      )}

      {reset && (
        <OpenBox
          show={reset}
          title={'Reset User Password'}
          cancel={() => setReset(false)}
          onPress={() => setReset(false)}
          press_title={'Ok'}
        >
          <Divider />
          <p style={{ margin: '0.5rem' }}>Request for reset token</p>
          <Button
            variant={'contained'}
            fullWidth
            onClick={async () => {
              setForm({ ...form, loading: true, success: null, errors: null })
              setReset(false)
              const res = await connect.POST({
                urlRoute: 'user?search=SEND-ME-TOKEN',
                data: { email },
              })

              if (res.success) {
                setForm({ ...form, success: 'Token sent successfully' })
              } else setForm({ ...form, errors: res.error })
            }}
          >
            Send
          </Button>
        </OpenBox>
      )}

      {loading && <Loading open={loading} />}
      {errors && <ErrorSnackbar msg={errors} />}
      {success && <SuccessSnackbar msg={success} />}
    </Box>
  )
}

export default HeaderView
