import Box from '@mui/material/Box'
import React from 'react'
import WrappedInBars from '../../../component/bars/WrappedInBars'
import CBSide from '../CBSide'
import CustomerDataSet from './dataset/CustomerDataSet'
import MeteringDataSet from './dataset/MeteringDataSet'
import QuesDataSet from './dataset/QuesDataset'
import SetBoxs from './dataset/SetBoxs'

function DataSet() {
  const [choice, setChoice] = React.useState({ datalink: '' })
  const { datalink } = choice
  const choose = (name, value) => setChoice({ ...choice, [name]: value })
  const cancel = () => setChoice({ ...choice, datalink: '' })

  return (
    <WrappedInBars UL={CBSide}>
      <Box marginY={2}>
        {datalink !== 'disco' && datalink !== 'form74' && (
          <QuesDataSet>
            <SetBoxs
              name={'datalink'}
              title={'Customer Record Dataset'}
              label={'form74'}
              open={datalink === 'form74'}
              click={choose}
            />
            <SetBoxs
              name={'datalink'}
              title={'Meter Record Dataset'}
              label={'disco'}
              open={datalink === 'disco'}
              click={choose}
            />
          </QuesDataSet>
        )}

        {datalink === 'disco' && <MeteringDataSet cancel={cancel} />}
        {datalink === 'form74' && <CustomerDataSet cancel={cancel} />}
      </Box>
    </WrappedInBars>
  )
}

export default DataSet
