import Box from '@mui/material/Box'
import Typography from '@mui/material/Typography'
import React from 'react'
import WrappedInBars from '../../../component/bars/WrappedInBars'
import UseAppContext from '../../../context/useAppContext'
import CBSide from '../CBSide'
import MeterBody from './meter-info/MeterBody'
import MeteringCard from './meter-info/MeteringCards'

function MeteringInfo() {
  const { meter_info } = React.useContext(UseAppContext)
  const av = Object.keys(meter_info).toString().startsWith('0')
  const [data, setData] = React.useState(null)
  const [show, setShow] = React.useState(false)

  const getEachData = (d) => {
    setData(d)
    setShow(true)
  }

  const cancel = () => {
    setShow(false)
    setData(null)
  }

  return (
    <WrappedInBars UL={CBSide}>
      <Box marginY={10}>
        {!show && !data && (
          <Typography variant="h4" component={'h6'} marginBottom={1}>
            Result Found
          </Typography>
        )}

        {av && !show && (
          <MeteringCard data={meter_info} onPress={getEachData} />
        )}
        {data && show && <MeterBody cancel={cancel} data={data} />}
      </Box>
    </WrappedInBars>
  )
}

export default MeteringInfo
