import Box from '@mui/material/Box'
import React from 'react'
import { Button, IconButton, Typography } from '@mui/material'
import Cancel from '@mui/icons-material/CancelOutlined'
import { STATES } from '../../../../../constants/coutries'
import ModelSelect from '../../../../../component/input/ModelSelect'

function FooterView({ parent_form, former = [], submit }) {
  const [form, setForm] = React.useState({ ...parent_form.coverage_data })
  const { franchise_states } = form

  const isthesame = former.join() === franchise_states.join()
  const isemty = franchise_states[0] === undefined || isthesame

  const getPickItem = (name, value) =>
    setForm({ ...form, franchise_states: [...franchise_states, value] })

  const removeItem = (c) =>
    setForm({
      ...form,
      franchise_states: franchise_states.filter((d) => d !== c),
    })
  return (
    <Box marginY={1}>
      <Typography variant="h6" component={'p'} marginTop={2}>
        Coverage Area
      </Typography>

      <Box sx={{ display: 'flex', alignItems: 'center' }}>
        <ModelSelect
          title="Add Franchise Area"
          onChange={getPickItem}
          items={STATES}
        />
        <Button
          variant="outlined"
          sx={{ marginX: 2 }}
          disabled={isemty}
          onClick={() => submit({ states: franchise_states.join() })}
        >
          Update Change
        </Button>
      </Box>

      {franchise_states.length > 0 && (
        <Box
          style={{
            display: 'grid',
            gridTemplateColumns: 'repeat(5,1fr)',
            justifyContent: 'flex-start',
            alignItems: 'center',
            border: '1px solid #ccc',
            padding: '0.5rem',
            width: '80%',
            margin: '0.8rem 0',
          }}
        >
          {franchise_states?.map((d, i) => (
            <Item key={i} title={d} onPress={() => removeItem(d)} />
          ))}
        </Box>
      )}
    </Box>
  )
}

function Item({ title, onPress }) {
  if (!title) return <></>
  return (
    <Box
      style={{
        display: 'flex',
        justifyContent: 'flex-start',
        alignItems: 'center',
      }}
    >
      <Typography variant="body1" component={'p'}>
        {title}
      </Typography>
      <IconButton onClick={onPress}>
        <Cancel sx={{ color: 'red' }} />
      </IconButton>
    </Box>
  )
}

export default FooterView
