import React from 'react'
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom'
import UseAppContext from '../../context/useAppContext'
import AddUser from './pages/AddUser'
import AdminDashoard from './pages/AdminDashoard'
import UserView from './pages/dashboard/view-user/UserView'

export default function Ads() {
  const { user } = React.useContext(UseAppContext)
  const { user_key } = user || {}

  return (
    <>
      <Router>
        <Routes>
          <Route path={`/`} element={<AdminDashoard />} />
          <Route path={`${user_key}/adduser`} element={<AddUser />} />
          <Route path={`${user_key}/view-user`} element={<UserView />} />
        </Routes>
      </Router>
    </>
  )
}
