import { Button, Divider, Typography } from '@mui/material'
import Box from '@mui/material/Box'
import React from 'react'
import Loading from '../../../../../component/alerts_boxes/Loading'
import OpenBox from '../../../../../component/alerts_boxes/OpenBox'
import {
  ErrorSnackbar,
  SuccessSnackbar,
} from '../../../../../component/alerts_boxes/SnackBar'
import SelectAutoWidth from '../../../../../component/input/SelectComponent'
import TextInput from '../../../../../component/input/TextInput'
import { RL } from '../../../../../constants/houseTypes'
import connect from '../../../../../context/connect_to_server'
import FooterView from './FooterView'

function BodyView({ data }) {
  const {
    fullName,
    email,
    email_verified,
    role,
    abbrv,
    telephone,
    right_to_share_profile,
    last_login_info,
    franchiseStates,
  } = data || {}
  const chises = franchiseStates || []
  const [tel, setTel] = React.useState(telephone || '')
  const [profile, setProfile] = React.useState(
    right_to_share_profile ? 'Allow' : 'Disallow'
  )
  const [open, setOpen] = React.useState(false)
  const [share, setShare] = React.useState(false)
  const [mrole, setMrole] = React.useState(role)
  const [abv, setAbv] = React.useState(abbrv)
  const [mrl, setMrl] = React.useState(false)
  const [ab, setAb] = React.useState(false)

  const [form, setForm] = React.useState({
    loading: false,
    success: null,
    errors: null,
    coverage_data: {
      franchise_states: [...chises],
    },
  })
  const { loading, success, errors } = form

  const submit = async (data = {}) => {
    setForm({ ...form, loading: true, success: null, errors: null })
    const res = await connect.PUT({ urlRoute: `user/${email}`, data })
    if (res.success) {
      setForm({
        ...form,
        loading: false,
        success: 'Profile updated successfully',
      })
    } else setForm({ ...form, loading: false, errors: res.error })
  }

  return (
    <Box marginY={2}>
      <Typography component={'h6'} variant={'h5'} sx={{ marginBottom: 2.5 }}>
        Contact Infomation
      </Typography>

      <Box className="grid-3" sx={{ marginY: 1 }}>
        <BX
          title={'Email Address'}
          semi={email}
          Press={() => alert('Connected')}
          // link={'Modify user name'}
        />
        <BX
          title={'Email Verified'}
          semi={email_verified ? 'Verified' : 'Not verified'}
        />
      </Box>

      <Box className="grid-3" sx={{ marginY: 1 }}>
        <BX title={'User Full Name'} semi={fullName} />
        <BX
          title={'Telephone'}
          semi={telephone || 'Not available'}
          Press={() => setOpen(true)}
          link={'Modify telephone'}
        />
      </Box>

      <Divider />

      <Typography
        component={'h6'}
        variant={'h5'}
        sx={{ marginBottom: 2.5, marginTop: 1 }}
      >
        Profile Infomation
      </Typography>

      <Box className="grid-3" sx={{ marginY: 1 }}>
        <BX
          title={'Role'}
          semi={role || 'Not available'}
          Press={() => setMrl(true)}
          link={'Modify Role'}
        />
        <BX
          title={'User Acroynms'}
          semi={abbrv || 'Not avaliable'}
          Press={() => setAb(true)}
          link={'Modify Abbrv.'}
        />
      </Box>

      <Box className="grid-3" sx={{ marginY: 1 }}>
        <BX
          title={'Right to Share Profile'}
          semi={right_to_share_profile ? 'TRUE' : 'FALSE'}
          Press={() => setShare(true)}
          link={'Modify'}
        />
        <BX title={'Last login'} semi={last_login_info || 'Not avaliable'} />
      </Box>

      {open && (
        <OpenBox
          show={open}
          title={'User Modification Module'}
          cancel={() => setOpen(false)}
          onPress={async () => {
            submit({ telephone: tel })
            setTel('')
            setOpen(false)
          }}
          press_title={'Update'}
        >
          <p style={{ margin: '0.5rem' }}>Modify user contact number </p>
          <TextInput
            onChange={(name, value) => setTel(value)}
            name={'tel'}
            value={tel}
            label={'Modify User Contact'}
          />
        </OpenBox>
      )}

      {share && (
        <OpenBox
          show={share}
          title={'Allow User to Share Profile'}
          cancel={() => setShare(false)}
          onPress={async () => {
            submit({
              right_to_share_profile: profile === 'Allow' ? true : false,
            })
            setProfile('')
            setShare(false)
          }}
          press_title={'Update'}
        >
          <Divider />
          <p style={{ margin: '0.5rem' }}>
            Allow user to share profile with others
          </p>
          <SelectAutoWidth
            handleInputChange={(name, value) => setProfile(value)}
            name={'profile'}
            value={profile}
            label={'Share Profile'}
            items={['Allow', 'Disallow']}
            size={'100%'}
          />
        </OpenBox>
      )}
      {mrl && (
        <OpenBox
          show={mrl}
          title={'Modify User Role'}
          cancel={() => setMrl(false)}
          onPress={async () => {
            submit({ role: mrole })
            setMrole('')
            setMrl(false)
          }}
          press_title={'Update'}
        >
          <Divider />
          <SelectAutoWidth
            handleInputChange={(name, value) => setMrole(value)}
            name={'mrole'}
            value={mrole}
            label={'Assign new Role'}
            items={RL}
            size={'100%'}
          />
        </OpenBox>
      )}
      {ab && (
        <OpenBox
          show={ab}
          title={'Modify User Abbreviation Name'}
          cancel={() => setAb(false)}
          onPress={async () => {
            submit({ abbrv: abv })
            setAbv('')
            setAb(false)
          }}
          press_title={'Update'}
        >
          <Divider />
          <TextInput
            onChange={(name, value) => setAbv(value)}
            name={'abv'}
            value={abv}
            label={'Modify User Abbrv.'}
          />
        </OpenBox>
      )}

      <FooterView
        parent_form={form}
        former={franchiseStates || []}
        submit={submit}
      />
      {loading && <Loading open={loading} />}
      {errors && <ErrorSnackbar msg={errors} />}
      {success && <SuccessSnackbar msg={success} />}
    </Box>
  )
}

function BX({ title, semi, link, Press }) {
  return (
    <Box>
      {/* <Typography component={'h6'} variant={'body1'} sx={{ paddingX: 1 }}>
        {title}
      </Typography> */}
      <h5 style={{ padding: '0 0.5rem' }}>{title}</h5>

      <Typography component={'p'} variant={'body2'} sx={{ paddingX: 1 }}>
        {semi}
      </Typography>

      {link && (
        <Button onClick={Press} sx={{ textTransform: 'initial' }}>
          {link}
        </Button>
      )}
    </Box>
  )
}

export default BodyView
