import FormGroup from '@mui/material/FormGroup'
import Checkbox from '@mui/material/Checkbox'
import FormControlLabel from '@mui/material/FormControlLabel'
import * as React from 'react'
import Button from '@mui/material/Button'
import ArrowBack from '@mui/icons-material/ArrowBackIosNewOutlined'
import SendIcon from '@mui/icons-material/Send'
import Stack from '@mui/material/Stack'

export const CHL = ({
  handleChange,
  label,
  checked = false,
  name,
  toggle,
  disabled,
  value,
}) => {
  const tog = toggle ? !toggle : true
  const alt = value ? value : tog

  return (
    <FormGroup>
      <FormControlLabel
        control={
          <Checkbox
            checked={checked}
            onChange={() => handleChange(name, alt)}
            inputProps={{ 'aria-label': 'controlled' }}
            disabled={disabled}
          />
        }
        label={label}
      />
    </FormGroup>
  )
}

export const BTN = ({
  onCancel,
  onSubmit,
  Icon,
  title = 'Next',
  disabled = false,
  cancel = 'Previous',
  varr = 'contained',
  top = 2.5,
  borderShown,
  x = 0,
}) => {
  return (
    <Stack direction="row" spacing={2} marginTop={top}>
      {onCancel && (
        <Button variant="outlined" startIcon={<ArrowBack />} onClick={onCancel}>
          {cancel}
        </Button>
      )}
      <Button
        variant={varr}
        disabled={disabled}
        startIcon={Icon ? <Icon /> : <SendIcon />}
        onClick={onSubmit}
        sx={{ border: borderShown, marginX: x }}
      >
        {title}
      </Button>
    </Stack>
  )
}
