import React from 'react'
import Chart from '@mui/icons-material/BarChart'
import Mail from '@mui/icons-material/MailOutline'
import House from '@mui/icons-material/House'
import Divider from '@mui/material/Divider'
import UseAppContext from '../../context/useAppContext'
import { useNavigate } from 'react-router-dom'
import ListParent from '../../component/bars/drawer/ListParent'
import LI from '../../component/bars/drawer/LI'
import { Book, GasMeter } from '@mui/icons-material'

function DsSide({ open }) {
  const { user, active, adjustMany } = React.useContext(UseAppContext)
  const { user_key } = user || {}
  const navigate = useNavigate()

  return (
    <ListParent>
      <LI
        open={open}
        text={'Dashboard'}
        active={active === 'dashboard'}
        Icon={Chart}
        onPress={() => {
          adjustMany({ active: 'dashboard' })
          return navigate(`/${user_key}/dashboard`)
        }}
      />
      <Divider />

      <LI
        open={open}
        text={'Metering Allocation'}
        Icon={GasMeter}
        active={active === 'store'}
        onPress={() => {
          adjustMany({ active: 'store' })
          navigate(`/${user_key}/store`)
        }}
      />
      <LI
        open={open}
        text={'File Upload (Property)'}
        Icon={Book}
        active={active === 'customer'}
        onPress={() => {
          adjustMany({ active: 'customer' })
          navigate(`/${user_key}/customer`)
        }}
      />

      <LI
        open={open}
        text={'Registered Property'}
        Icon={House}
        active={active === 'property'}
        onPress={() => {
          adjustMany({ active: 'property' })
          navigate(`/${user_key}/property`)
        }}
      />
      <LI
        open={open}
        text={'Activities'}
        Icon={Mail}
        active={active === 'logs'}
        onPress={() => {
          adjustMany({ active: 'logs' })
          navigate(`/${user_key}/logs`)
        }}
      />
      <Divider />
    </ListParent>
  )
}

export default DsSide
