import { Paper, Typography } from '@mui/material'
import React from 'react'

const QuesDataSet = ({ Press, children }) => {
  return (
    <div
      style={{
        height: '85vh',
        justifyContent: 'center',
        alignItems: 'center',
        display: 'flex',
      }}
    >
      <Paper
        style={{
          padding: 20,
          height: '63vh',
          width: 350,
          margin: '0 auto',
          borderRadius: '0.5rem',
        }}
        sx={{
          justifyContent: 'center',
          alignItems: 'center',
          display: 'flex',
          flexDirection: 'column',
          gap: '.5rem',
        }}
      >
        <Typography variant={'h6'} component={'h6'} marginY={1}>
          Select Dataset Module
        </Typography>
        {children}
      </Paper>
    </div>
  )
}

export default QuesDataSet
