import { Button, Divider, Typography } from '@mui/material'
import Box from '@mui/material/Box'
import React from 'react'
import Loading from '../../component/alerts_boxes/Loading'
import OpenBox from '../../component/alerts_boxes/OpenBox'
import {
  ErrorSnackbar,
  SuccessSnackbar,
} from '../../component/alerts_boxes/SnackBar'

import TextInput from '../../component/input/TextInput'
import connect from '../../context/connect_to_server'

import ProfileFooterView from './ProfileFooterView'

function ProfileBodyView({ data }) {
  const {
    fullName,
    email,
    email_verified,
    role,
    abbrv,
    telephone,
    right_to_share_profile,
    last_login_info,
    franchiseStates,
  } = data || {}
  const chises = franchiseStates || []
  const [tel, setTel] = React.useState(telephone || '')
  const [open, setOpen] = React.useState(false)

  const [form, setForm] = React.useState({
    loading: false,
    success: null,
    errors: null,
    coverage_data: {
      franchise_states: [...chises],
    },
  })
  const { loading, success, errors } = form

  const submit = async (data = {}) => {
    setForm({ ...form, loading: true, success: null, errors: null })
    const res = await connect.PUT({ urlRoute: `user/${email}`, data })
    if (res.success) {
      setForm({
        ...form,
        loading: false,
        success: 'Profile updated successfully. Try relogging to see change',
      })
    } else setForm({ ...form, loading: false, errors: res.error })
  }

  return (
    <Box marginY={2}>
      <Typography component={'h6'} variant={'h5'} sx={{ marginBottom: 2.5 }}>
        Contact Infomation
      </Typography>

      <Box className="grid-3" sx={{ marginY: 1 }}>
        <BX title={'Email Address'} semi={email} />
        <BX
          title={'Email Verified'}
          semi={email_verified ? 'Verified' : 'Not verified'}
        />
      </Box>

      <Box className="grid-3" sx={{ marginY: 1 }}>
        <BX title={'User Full Name'} semi={fullName} />
        <BX
          title={'Telephone'}
          semi={telephone || 'Not available'}
          Press={() => setOpen(true)}
          link={'Modify telephone'}
        />
      </Box>

      <Divider />

      <Typography
        component={'h6'}
        variant={'h5'}
        sx={{ marginBottom: 2.5, marginTop: 1 }}
      >
        Profile Infomation
      </Typography>

      <Box className="grid-3" sx={{ marginY: 1 }}>
        <BX title={'Role'} semi={role || 'Not available'} />
        <BX title={'User Acroynms'} semi={abbrv || 'Not avaliable'} />
      </Box>

      <Box className="grid-3" sx={{ marginY: 1 }}>
        <BX
          title={'Right to Share Profile'}
          semi={right_to_share_profile ? 'TRUE' : 'FALSE'}
        />
        <BX title={'Last login'} semi={last_login_info || 'Not avaliable'} />
      </Box>

      {open && (
        <OpenBox
          show={open}
          title={'User Modification Module'}
          cancel={() => setOpen(false)}
          onPress={async () => {
            submit({ telephone: tel })
            setTel('')
            setOpen(false)
          }}
          press_title={'Update'}
        >
          <p style={{ margin: '0.5rem' }}>Modify user contact number </p>
          <TextInput
            onChange={(name, value) => setTel(value)}
            name={'tel'}
            value={tel}
            label={'Modify User Contact'}
          />
        </OpenBox>
      )}

      {['MAP', 'DISCO'].includes(role) && (
        <ProfileFooterView franchise_states={franchiseStates || []} />
      )}
      {loading && <Loading open={loading} />}
      {errors && <ErrorSnackbar msg={errors} />}
      {success && <SuccessSnackbar msg={success} />}
    </Box>
  )
}

function BX({ title, semi, link, Press }) {
  return (
    <Box>
      <h5 style={{ padding: '0 0.5rem' }}>{title}</h5>

      <Typography component={'p'} variant={'body2'} sx={{ paddingX: 1 }}>
        {semi}
      </Typography>

      {link && (
        <Button onClick={Press} sx={{ textTransform: 'initial' }}>
          {link}
        </Button>
      )}
    </Box>
  )
}

export default ProfileBodyView
