import Typography from '@mui/material/Typography'
import Box from '@mui/material/Box'
import React from 'react'
import WrappedInBars from '../../../component/bars/WrappedInBars'
import AdsSide from '../AdsSide'
import ProgressSteps from '../../../component/ProgressSteps'
import { CHL } from '../../../component/input/InputEssential'
import BasicInformation from './adduser/BasicInformation'
import BasicAddress from './adduser/BasicAddress'
import OtherInformation from './adduser/OtherInformation'
import Franchises from './adduser/Franchises'
import { generateString } from './adduser/PasswordGen'
import AddUserFinish from './adduser/AddUserFinish'
import Loading from '../../../component/alerts_boxes/Loading'
import {
  ErrorSnackbar,
  SuccessSnackbar,
} from '../../../component/alerts_boxes/SnackBar'
import UseAppContext from '../../../context/useAppContext'

function AddUser() {
  const { adjustMany } = React.useContext(UseAppContext)
  const pass = generateString()
  const [form, setForm] = React.useState({
    counter: 0,
    basic: true,
    locs: false,
    oth_info: false,
    coverage: false,
    individual: false,
    company: true,
    finish: false,
    loading: false,
    errors: null,
    success: null,
    basic_data: {
      fname: '',
      lname: '',
      email: '',
      telephone: '',
      identity_id: '',
      identity_type: '',
    },
    locs_data: {
      street_number: '',
      street_name: '',
      city: '',
      province: '',
      lat: '',
      lng: '',
    },
    oth_info_data: {
      role: '',
      abbrv: '',
      right_to_share_profile: false,
    },
    coverage_data: {
      franchise_states: [],
    },
    finish_data: {
      password: pass,
    },
  })

  React.useEffect(() => {
    ;(function () {
      adjustMany({ active: 'adduser' })
    })()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])
  const handleChange = (name, value) =>
    setForm({ ...form, individual: false, company: false, [name]: value })

  const onPageChange = (name, value, pre, th, vl) =>
    setForm({
      ...form,
      locs: false,
      basic: false,
      oth_info: false,
      coverage: false,
      finish: false,
      counter: pre ? counter - 1 : counter + 1,
      [name]: value,
      [th]: vl,
    })

  const {
    individual,
    company,
    counter,
    locs,
    basic,
    oth_info,
    coverage,
    finish,
    loading,
    errors,
    success,
  } = form

  return (
    <WrappedInBars UL={AdsSide}>
      <Box paddingY={1}>
        <Typography component={'h2'} variant={'h5'} marginBottom={1}>
          Add a User
        </Typography>

        <ProgressSteps
          steps={[
            'User Basic Information',
            'Address & Location',
            'Other Relevant Information',
            'Add Coverage Area',
            'Finish',
          ]}
          activeStep={counter}
        />

        {basic && (
          <Typography component={'p'} variant={'body1'} marginTop={2}>
            To get started, fill out some basic information about who you're
            adding as a user.
          </Typography>
        )}

        {basic && (
          <Box>
            <CHL
              label={'Company / Corporate Body'}
              handleChange={handleChange}
              checked={company}
              name={'company'}
            />
            <CHL
              label={'Individaul'}
              handleChange={handleChange}
              checked={individual}
              name={'individual'}
            />
          </Box>
        )}

        {basic && (
          <BasicInformation parent_form={form} onPageChange={onPageChange} />
        )}

        {locs && (
          <BasicAddress parent_form={form} onPageChange={onPageChange} />
        )}

        {oth_info && (
          <OtherInformation
            parent_form={form}
            onPageChange={onPageChange}
            handleChange={handleChange}
          />
        )}

        {coverage && (
          <Franchises
            parent_form={form}
            onPageChange={onPageChange}
            handleChange={handleChange}
          />
        )}
        {finish && (
          <AddUserFinish
            parent_form={form}
            onPageChange={onPageChange}
            _state={setForm}
          />
        )}
      </Box>

      {loading && <Loading open={loading} />}
      {errors && <ErrorSnackbar msg={errors} />}
      {success && <SuccessSnackbar msg={success} />}
    </WrappedInBars>
  )
}

export default AddUser
