import React from 'react'
import Reset from '@mui/icons-material/Restore'

import Suspend from '@mui/icons-material/PersonAddDisabled'
import Box from '@mui/material/Box'
import { Avatar, Button, Typography, Divider } from '@mui/material'
import connect from '../../context/connect_to_server'
import Loading from '../../component/alerts_boxes/Loading'
import OpenBox from '../../component/alerts_boxes/OpenBox'
import {
  ErrorSnackbar,
  SuccessSnackbar,
} from '../../component/alerts_boxes/SnackBar'
import SHAddUser from '../share-profile/SHAddUser'
import SHCards from '../share-profile/shuser/SHCards'

function ProfileHeaderView({ data, Side }) {
  const { fullName, email } = data || {}
  const ab = fullName ? fullName.substr(0, 3) : ''
  const [reset, setReset] = React.useState(false)
  const [share, setShare] = React.useState(false)
  const [shu, setShu] = React.useState(false)

  const [form, setForm] = React.useState({
    loading: false,
    success: null,
    errors: null,
  })
  const { loading, success, errors } = form

  React.useEffect(() => {
    ;(async function () {
      setForm({ ...form, loading: true, success: null, errors: null })
      const res = await connect.GET({
        urlRoute: 'user',
        data: { search: 'OWNED' },
      })
      if (res.success) {
        setForm({ ...form, loading: false })
        setShu(res.data)
      } else setForm({ ...form, loading: false })
    })()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const submit = async (data = {}, method) => {
    setForm({ ...form, loading: true, success: null, errors: null })
    setReset(false)
    const res = await connect[method ? 'POST' : 'PUT']({
      urlRoute: method ? 'user' : `user/${email}`,
      data,
    })
    if (res.success) {
      setForm({
        ...form,
        loading: false,
        success: method
          ? 'Profile shared successfully...'
          : 'Profile updated successfully',
      })
    } else setForm({ ...form, loading: false, errors: res.error })
  }

  return (
    <Box
      style={{
        display: 'flex',
        justifyContent: 'flex-start',
        alignItems: 'center',
      }}
      marginY={2}
    >
      <Avatar
        sx={{
          width: 90,
          height: 90,
          marginRight: 5,
          fontSize: 40,
          textTransform: 'uppercase',
        }}
      >
        {ab}
      </Avatar>
      <Box>
        <Typography component={'h6'} variant={'h4'}>
          {fullName}
        </Typography>
        <Box
          style={{
            display: 'flex',
            justifyContent: 'flex-start',
            alignItems: 'center',
          }}
        >
          <Button startIcon={<Reset />} onClick={() => setReset(true)}>
            Share Profile
          </Button>
          <Button
            startIcon={<Suspend />}
            sx={{ marginLeft: 1 }}
            onClick={() => setShare(true)}
          >
            View Share Profile
          </Button>
        </Box>
      </Box>

      {reset && (
        <OpenBox
          show={reset}
          title={'Share Profile'}
          cancel={() => setReset(false)}
          onPress={() => setReset(false)}
          press_title={'Finish'}
        >
          <Divider />

          <SHAddUser submit={submit} />
        </OpenBox>
      )}

      {share && (
        <OpenBox
          show={share}
          title={'View Shared Profile User'}
          cancel={() => setShare(false)}
          onPress={() => setShare(false)}
          press_title={'Finish'}
        >
          <Divider />

          {shu && shu.map((d, i) => <SHCards data={d} key={i} />)}
        </OpenBox>
      )}

      {loading && <Loading open={loading} />}
      {errors && <ErrorSnackbar msg={errors} />}
      {success && <SuccessSnackbar msg={success} />}
    </Box>
  )
}

export default ProfileHeaderView
