import { Box, Button, Divider, Typography } from '@mui/material'
import React from 'react'

function BasicInformation({ data }) {
  const {
    meterNumber,
    cartonID,
    meterSeries,
    meterOwner,
    defaultUnit,
    capacity,
    volt,
    phase,
    category,
    meterModel,
    meterType,
    meterMake,
  } = data || {}
  return (
    <Box marginY={1}>
      <Box className="grid-3" sx={{ marginY: 1 }}>
        <BX title={'Meter Provider:'} semi={meterOwner} />
        <BX title={'Carton ID:'} semi={cartonID} />
      </Box>

      <Box className="grid-3" sx={{ marginY: 1 }}>
        <BX title={'Meter Series:'} semi={meterSeries} />
        <BX title={'Meter Number:'} semi={meterNumber} />
      </Box>

      <Box className="grid-3" sx={{ marginY: 1 }}>
        <BX title={'Meter Capacity:'} semi={capacity} />
        <BX title={'Initial Unit / Factory Unit:'} semi={defaultUnit || 0} />
      </Box>
      <Divider />

      <Box className="grid-3" sx={{ marginY: 1 }}>
        <BX title={'Voltage Level:'} semi={volt} />
        <BX title={'Meter Phase:'} semi={phase} />
      </Box>

      <Box className="grid-3" sx={{ marginY: 1 }}>
        <BX title={'Meter Category:'} semi={category} />
        <BX title={'Model:'} semi={meterModel} />
      </Box>

      <Box className="grid-3" sx={{ marginY: 1 }}>
        <BX title={'Meter Type:'} semi={meterType} />
        <BX title={'Meter Make:'} semi={meterMake} />
      </Box>
      <Divider />
    </Box>
  )
}

function BX({ title, semi, link, Press }) {
  return (
    <Box>
      {/* <Typography
        component={'h6'}
        variant={'h6'}
        sx={{ paddingX: 1, fontSize: 15 }}
      >
        {title}
      </Typography> */}
      <h5 style={{ padding: '0 0.5rem' }}>{title}</h5>

      <Typography component={'p'} variant={'body2'} sx={{ paddingX: 1 }}>
        {semi}
      </Typography>

      {link && (
        <Button onClick={Press} sx={{ textTransform: 'initial' }}>
          {link}
        </Button>
      )}
    </Box>
  )
}

export default BasicInformation
