import Grid from '@mui/material/Grid'
import Box from '@mui/material/Box'
import React from 'react'
import SelectAutoWidth from '../../../../component/input/SelectComponent'
import TextInput from '../../../../component/input/TextInput'
import { BTN } from '../../../../component/input/InputEssential'
import { IconButton, Typography } from '@mui/material'
import Close from '@mui/icons-material/Close'
import { STATES } from '../../../../constants/coutries'
import connect from '../../../../context/connect_to_server'
import Loading from '../../../../component/alerts_boxes/Loading'
import { ErrorSnackbar } from '../../../../component/alerts_boxes/SnackBar'
import { date_config } from '../../../../helper/essential'

function MeteringDataSet({ cancel }) {
  const et = date_config().fdate
  const [form, setForm] = React.useState({
    disco_acknowledgement_by: '',
    disco_allocation_to: '',
    meter_installation_location: '',
    installation_status: '',
    allocation_date: et,
    acknowledged_date: et,
    installation_date: et,
    uploaded_by: '',
    loading: false,
    errors: null,
  })
  const {
    disco_acknowledgement_by,
    disco_allocation_to,
    meter_installation_location,
    acknowledged_date,
    installation_date,
    installation_status,
    allocation_date,
    uploaded_by,
    loading,
    errors,
  } = form
  const onChange = (name, value) => setForm({ ...form, [name]: value })

  const submit = async () => {
    setForm({ ...form, loading: false, errors: null })
    const res = await connect.GET({
      urlRoute: 'disco',
      params: 'search=file',
      data: { ...form, errors: null, loading: false },
    })
    if (res.success) {
      setForm({ ...form, loading: false })
    } else {
      setForm({ ...form, loading: false, errors: res.error })
    }
  }

  return (
    <Box marginY={10}>
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
        }}
      >
        <Box>
          <Typography component={'h4'} variant={'h6'} marginTop={2}>
            METER DATASET MODULE.
          </Typography>
          <Typography component={'p'} variant={'subtitle1'}>
            Please note, your request limit has been set to 20,000.
          </Typography>
        </Box>
        <IconButton onClick={cancel}>
          <Close sx={{ color: 'red' }} />
        </IconButton>
      </Box>
      <Grid container spacing={2}>
        <Grid item xs={4}>
          <SelectAutoWidth
            handleInputChange={onChange}
            name={'uploaded_by'}
            value={uploaded_by}
            label={'Select Meter Provider'}
            items={[
              'Voters Card',
              'Driver Licence',
              'International Passport',
              'Certificate of Incorporation',
              'Other',
            ]}
            size={'100%'}
          />
        </Grid>
        <Grid item xs={4}>
          <SelectAutoWidth
            handleInputChange={onChange}
            name={'disco_acknowledgement_by'}
            value={disco_acknowledgement_by}
            label={'Meter Allocated To (DISCO)'}
            items={[
              'Voters Card',
              'Driver Licence',
              'International Passport',
              'Certificate of Incorporation',
              'Other',
            ]}
            size={'100%'}
          />
        </Grid>
      </Grid>

      <Grid container spacing={2}>
        <Grid item xs={4}>
          <SelectAutoWidth
            handleInputChange={onChange}
            name={'disco_allocation_to'}
            value={disco_allocation_to}
            label={'Meter Allocated To (INSTALLER)'}
            items={[
              'Voters Card',
              'Driver Licence',
              'International Passport',
              'Certificate of Incorporation',
              'Other',
            ]}
            size={'100%'}
          />
        </Grid>
        <Grid item xs={4}>
          <SelectAutoWidth
            handleInputChange={onChange}
            name={'meter_installation_location'}
            value={meter_installation_location}
            label={'Select Meter Location'}
            items={STATES}
            size={'100%'}
          />
        </Grid>
      </Grid>

      <Grid container spacing={2}>
        <Grid item xs={4}>
          <SelectAutoWidth
            handleInputChange={onChange}
            name={'installation_status'}
            value={installation_status}
            label={'Select Installation Status *'}
            items={[
              'Voters Card',
              'Driver Licence',
              'International Passport',
              'Certificate of Incorporation',
              'Other',
            ]}
            size={'100%'}
          />
        </Grid>
        <INPUT
          onChange={onChange}
          name={'allocation_date'}
          value={allocation_date}
          required={true}
          label={'Allocation Date'}
          input_type={'date'}
        />
      </Grid>

      <Grid container spacing={2}>
        <INPUT
          onChange={onChange}
          name={'acknowledged_date'}
          value={acknowledged_date}
          required={true}
          label={'Disco Acknowledged Date'}
          input_type={'date'}
        />

        <INPUT
          onChange={onChange}
          name={'installation_date'}
          value={installation_date}
          required={true}
          label={'Meter Installation Date'}
          input_type={'date'}
        />
      </Grid>

      {loading && <Loading open={loading} />}
      {errors && <ErrorSnackbar />}

      <BTN title="Send" onSubmit={submit} />
    </Box>
  )
}

function INPUT({
  onChange,
  name,
  value,
  required,
  span = 4,
  label,
  input_type,
}) {
  return (
    <Grid item xs={span}>
      <TextInput
        onChange={onChange}
        name={name}
        value={value}
        required={required}
        label={label}
        input_type={input_type}
      />
    </Grid>
  )
}

export default MeteringDataSet
