import { Box, Grid, Typography } from '@mui/material'
import * as React from 'react'
import { useNavigate } from 'react-router-dom'
import Loading from '../../../component/alerts_boxes/Loading'
import OpenBox from '../../../component/alerts_boxes/OpenBox'
import { ErrorSnackbar } from '../../../component/alerts_boxes/SnackBar'
import WrappedInBars from '../../../component/bars/WrappedInBars'
import { BTN, CHL } from '../../../component/input/InputEssential'
import SelectAutoWidth from '../../../component/input/SelectComponent'
import TextInput from '../../../component/input/TextInput'
import connect from '../../../context/connect_to_server'
import UseAppContext from '../../../context/useAppContext'
import DsSide from '../DsSide'

const AllocationPage = () => {
  const { adjustMany, store_items, user } = React.useContext(UseAppContext)
  const { cartonID, pop } = store_items || {}
  const { user_key, role, franchiseStates } = user || {}
  const [notify, setNotify] = React.useState(false)

  const navigate = useNavigate()

  const [form, setForm] = React.useState({
    allocateAll: true,
    owner: '',
    opt: [],
    currentStore: '',
    piece: '',
    ed: false,
    recipient: '',
    form74: '',
    destinationStore: '',
    loading: false,
    errors: null,
    alo: null,
    installer: null,
  })

  React.useEffect(() => {
    ;(async function () {
      if (!store_items) return navigate(`/${user_key}/dashboard`)
      adjustMany({ active: 'store' })
      setNotify(true)
      const res = await connect.GET({
        params: `search=selection&skip=ALLOCATE:${role}`,
        urlRoute: 'user',
      })
      if (res.success) {
        const np = [...new Set(res.data.map((a) => a.abbrv))]
        setForm({ ...form, opt: np, installer: res.data })
      } else
        setForm({
          ...form,
          errors: 'No registered INSTALLER found',
          loading: false,
        })
    })()

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const { allocateAll, piece, owner, recipient, form74 } = form
  const onChange = (name, value) => setForm({ ...form, [name]: value })

  const submit = async () => {
    const pics = typeof piece === 'string' ? +piece : piece
    const pcs =
      pics > form74 ? form74 : pics > 0 && pics < form74 ? pics : form74
    setForm({ ...form, loading: true, errors: null })
    const owner_ = form.installer.find((d) => d.abbrv === owner)
    const res = await connect.PUT({
      urlRoute: `disco/${cartonID}`,
      params: `limit=${pcs}&search=ALLOCATE`,
      data: {
        disco_allocation_to: `${owner_.fullName}:${owner_.email}`,
        allocation_customer_id: form.alo,
        receiver: `${owner_.fullName}:${owner_.email}`,
      },
    })

    if (res.success) {
      onChange('loading', false)
      navigate(`/${user_key}/store`)
    } else setForm({ ...form, loading: false, errors: res.error })
  }

  const loadAddress = async (where) => {
    setForm({ ...form, loading: true, errors: null })
    const re = await connect.GET({
      urlRoute: 'form74',
      params: `is_certified=true&has_allocation=false&state=${where}`,
    })
    if (re.success) {
      const alo =
        re.data.length > 0 ? re.data.map((o) => o._id || o.customer_id) : []
      setForm({
        ...form,
        form74: re.data.length > 0 ? re.data.length : '',
        alo,
      })
    } else navigate(`/${user_key}/store`)
  }

  return (
    <WrappedInBars UL={DsSide}>
      <Box marginY={10}>
        <Typography variant="h4" component={'h6'} marginBottom={1}>
          Allocation by Carton Ref: {cartonID || ''}
        </Typography>

        <Typography variant="body1" component={'p'} marginBottom={1}>
          Total number in this category: {pop || 0} Meter(s)
        </Typography>

        {!form74 && recipient && (
          <OpenBox
            show={!form74 && recipient}
            title={'Certified Properties Finder'}
            onPress={() => loadAddress(recipient)}
            press_title={'Find'}
          >
            <Typography>
              Click below to search for <strong>verified properties</strong>{' '}
              that matches <strong>THIS </strong>
              location: {recipient}
            </Typography>
          </OpenBox>
        )}

        {notify && (
          <OpenBox
            show={notify}
            title={'Notification'}
            onPress={() => setNotify(false)}
            press_title={'Ok'}
          >
            <Typography>
              Please note, You won't be able to proceed if all applicant
              properties are not certified
            </Typography>
          </OpenBox>
        )}
        <WR>
          <INPUT
            label={'Select Property Place (Province)'}
            onChange={onChange}
            name={'recipient'}
            value={recipient}
            items={franchiseStates}
          />

          <INPUT
            onChange={onChange}
            name={'form74'}
            value={form74}
            label={'Certified Properties Count'}
            disable={true}
          />
        </WR>

        <WR>
          <INPUT
            onChange={onChange}
            name={'owner'}
            value={owner}
            items={form.opt}
            label={'Select Installer Name'}
          />

          <INPUT
            label={'Allocate Pieces from Carton'}
            onChange={onChange}
            name={'piece'}
            value={piece}
            disable={allocateAll && true}
          />
        </WR>

        <CHL
          label={'Allocate all ' + pop + ' items(s) '}
          handleChange={onChange}
          checked={allocateAll}
          toggle={allocateAll}
          name={'allocateAll'}
          disabled={piece && true}
        />

        {form.loading && <Loading open={form.loading} />}
        {form.errors && <ErrorSnackbar msg={form.errors} />}

        <BTN
          title="Send"
          onSubmit={submit}
          disabled={!owner || !form74 || (!allocateAll && !piece)}
        />
      </Box>
    </WrappedInBars>
  )
}

function INPUT({
  onChange,
  name,
  value,
  required,
  span = 4,
  label,
  input_type,
  items,
  disable,
}) {
  return (
    <Grid item xs={span}>
      {items ? (
        <SelectAutoWidth
          handleInputChange={onChange}
          name={name}
          value={value}
          label={label}
          items={items}
          size={'100%'}
        />
      ) : (
        <TextInput
          onChange={onChange}
          name={name}
          value={value}
          required={required}
          label={label}
          input_type={input_type}
          disabled={disable}
        />
      )}
    </Grid>
  )
}

function WR({ sp = 2, children }) {
  return (
    <Grid container spacing={sp}>
      {children}
    </Grid>
  )
}

export default AllocationPage
