import { Check, CloudCircle } from '@mui/icons-material'
import { Paper, Typography } from '@mui/material'
import { COLORS } from '../../../constants'

export const UploadFileContainer = ({ height, onChange, name, ext, value }) => {
  return (
    <>
      <input
        type={'file'}
        id="file"
        accept=""
        style={{ visibility: 'hidden' }}
      />

      <div
        style={{
          width: '60%',
          padding: 20,
          margin: '0 auto',
          marginTop: 10,
          cursor: 'pointer',
        }}
        id="file-upload"
        onClick={() => {
          if (onChange) {
            const file = document.getElementById('file')
            file.click()
            file.onchange = (e) =>
              onChange(name, document.getElementById('file').files[0])
          }
        }}
      >
        <Paper elevation={1} sx={{ padding: 1 }}>
          <div
            style={{
              border: '5px dotted #ccc',
              textAlign: 'center',
              padding: 20,
              fontSize: '1rem',
            }}
          >
            {!value ? (
              <CloudCircle sx={{ color: COLORS.primary, fontSize: '5rem' }} />
            ) : (
              <Check sx={{ color: 'green', fontSize: '5rem' }} />
            )}
            {!value && (
              <Typography>
                {' '}
                *File Extention should be
                <span
                  style={{
                    fontWeight: 'bold',
                    color: COLORS.primary,
                    fontSize: '1rem',
                  }}
                >
                  {' '}
                  {ext || '.CSV'}
                </span>{' '}
                format*{' '}
              </Typography>
            )}

            {!value && (
              <p>
                {' '}
                *Max File Size
                <span
                  style={{
                    fontWeight: 'bold',
                    color: COLORS.primary,
                    fontSize: '1rem',
                  }}
                >
                  {' '}
                  10MB
                </span>{' '}
                *{' '}
              </p>
            )}

            {value && <h4>File selected successfully...</h4>}
            {value && <Typography>File name: {value.name}</Typography>}
          </div>
        </Paper>
      </div>
    </>
  )
}
