import { GasMeter, House, HourglassBottom, CarCrash } from '@mui/icons-material'
import { Box, Typography } from '@mui/material'
import React from 'react'
import { Link } from 'react-router-dom'
import { COLORS } from '../../../../constants'
import UseAppContext from '../../../../context/useAppContext'
import { PhasePane, TopBox, TopBxOutline } from './TopMidBox'

function TopBack({ data = {} }) {
  const { signal, dsk, user } = React.useContext(UseAppContext)
  const { user_key } = user || {}
  const { header_stats, phase } = data || {}
  const {
    disco_total_uploads,
    map_total_uploads,
    total_meter,
    disco_allocations,
    total_meter_applicant,
    unmetered_applicant,
    metersOnTransitCount,
    installation_count,
  } = header_stats || {}

  const { single, three } = phase || {}
  const sn = single || {}
  const th = three || {}

  let page
  if (dsk) page = `${dsk.toUpperCase()} Summary View`
  else page = 'Overall View'

  return (
    <Box
      sx={{
        backgroundColor: COLORS.primary,
        // height: '50vh',
        zIndex: -1,
        paddingLeft: '11.5rem',
      }}
    >
      <Box
        sx={{
          padding: 1,
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          gap: '1rem',
        }}
      >
        <TopBox
          value={total_meter || 0}
          title="Project Goal / Target"
          Icon={HourglassBottom}
        />
        <TopBox
          bg="rgb(213, 149, 241,0.8)"
          value={map_total_uploads || 0}
          title="Meter Supplied"
          Icon={GasMeter}
        />
        <TopBox
          value={metersOnTransitCount || 0}
          title="Meters on Transit"
          Icon={CarCrash}
        />
        <TopBox
          bg="rgb(213, 149, 241,0.8)"
          value={total_meter_applicant || 0}
          title="No. of Meter Request"
          Icon={House}
        />
      </Box>

      <Box
        sx={{
          padding: 1,
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          gap: '1rem',
        }}
      >
        <TopBxOutline
          value={disco_total_uploads || 0}
          title="Meters Received By DISCO(s)"
        />
        <TopBxOutline
          value={disco_allocations || 0}
          title="DISCOs Allocation to INSTALLER"
        />
        <TopBxOutline
          value={installation_count || 0}
          title="Meter Installed by INSTALLER"
        />
        <TopBxOutline
          value={unmetered_applicant || 0}
          title="Pending Allocation (DISCO)"
        />
      </Box>

      <Box
        sx={{
          padding: 0.5,
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          width: '75%',
          margin: 0.5,
          paddingBottom: 1,
        }}
      >
        <Typography
          sx={{ color: COLORS.white }}
          component={'h5'}
          variant={'h6'}
        >
          <Link to={`/${user_key}/home`} style={{ color: COLORS.white }}>
            Home
          </Link>{' '}
          / {page}
        </Typography>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
          }}
        >
          {/* <TpTxt value={4949589} />
          <TpTxt value={43382372} /> */}
          {signal !== 'DISCO' && (
            <PhasePane
              title="1-PHASE:"
              value={sn.total}
              sent={sn.sent}
              received={sn.received}
            />
          )}
          {signal !== 'DISCO' && (
            <PhasePane
              title="3-PHASE:"
              value={th.total}
              sent={th.sent}
              received={th.received}
            />
          )}

          {signal === 'DISCO' && (
            <PhasePane
              title="TOTAL:"
              value={phase.total}
              sent={sn}
              received={th}
              stxt={'1-PHASE'}
              ttxt={'3-PHASE'}
            />
          )}
        </Box>
      </Box>
    </Box>
  )
}

export default TopBack
