import React from 'react'
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom'
import WrappedInBars from '../../component/bars/WrappedInBars'
import UseAppContext from '../../context/useAppContext'
import Logger from '../activities/Logger'
import ProfileUserView from '../profile/ProfileUserView'
import AllocationPage from './allocation/AllocationPage'
import EachStoreItem from './allocation/EachStoreItem'
import StoreItem from './allocation/StoreItem'
import DiscoCreateCustomerCSV from './csvUploads/DiscoCreateCustomerCSV'
import DsMpDashboard from './DsMpDashboard'
import MPSide from './DsSide'
import RegisteredProps from './property/RegisteredProps'

export default function Ds() {
  const { user } = React.useContext(UseAppContext)
  const { user_key } = user || {}

  const DB = () => {
    return <WrappedInBars UL={MPSide}></WrappedInBars>
  }

  return (
    <>
      <Router>
        <Routes>
          <Route path={`/`} element={<DB />} />
          <Route path={`${user_key}/dashboard`} element={<DsMpDashboard />} />
          <Route path={`${user_key}/store`} element={<StoreItem />} />
          <Route
            path={`${user_key}/store-record`}
            element={<EachStoreItem />}
          />

          <Route
            path={`${user_key}/customer`}
            element={<DiscoCreateCustomerCSV />}
          />

          <Route path={`${user_key}/allocate`} element={<AllocationPage />} />
          <Route path={`${user_key}/logs`} element={<Logger Side={MPSide} />} />
          <Route
            path={`${user_key}/profile`}
            element={<ProfileUserView Side={MPSide} />}
          />

          <Route
            path={`${user_key}/property`}
            element={<RegisteredProps Side={MPSide} />}
          />
        </Routes>
      </Router>
    </>
  )
}
