import { Box, Typography } from '@mui/material'
import React from 'react'
import UseAppContext from '../../../context/useAppContext'
import SearchBar from './SearchBar'
import Loading from '../../../component/alerts_boxes/Loading'
import { ErrorSnackbar } from '../../../component/alerts_boxes/SnackBar'
import connect from '../../../context/connect_to_server'

function SearchHeader() {
  const { adjustMany, user } = React.useContext(UseAppContext)
  const { abbrv } = user || {}
  const [loading, setLoading] = React.useState(false)
  const [errors, setErrors] = React.useState(null)
  const [data, setData] = React.useState(null)

  const reload = async (offset, limit, find) => {
    setLoading(true)
    const vv = {
      disco: abbrv,
      ...(offset && limit ? { offset, limit } : ''),
    }
    const res = await connect.GET({
      urlRoute: 'form74',
      data: find ? { customer_id: find } : vv,
      params: 'has_allocation=false',
    })
    if (res.success) {
      setLoading(false)
      adjustMany({ list: res.data })
      setData(res.data)
    } else {
      setLoading(false)
      setErrors(res.error)
    }
  }
  React.useEffect(() => {
    reload()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  console.log(data)
  return (
    <Box
      sx={{
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        marginBottom: 1.2,
      }}
    >
      <Typography component={'h4'} variant={'h4'}>
        Registered Request(s) for Meter
      </Typography>
      {data && (
        <SearchBar
          onPress={(d) => reload(undefined, undefined, d)}
          data={data}
        />
      )}
      {loading && <Loading open={loading} />}
      {errors && <ErrorSnackbar msg={`No registered property found`} />}
    </Box>
  )
}

export default SearchHeader
