import { Box, IconButton, Paper, Typography } from '@mui/material'
import React from 'react'
import { ArrowForward, Sync } from '@mui/icons-material'
import { assets, COLORS } from '../../../../constants'
import { DSK, GoPreview } from './TopMidBox'
import UseAppContext from '../../../../context/useAppContext'
import { clean_up_data, map_prov } from './CleanUpData'
import { ErrorSnackbar } from '../../../../component/alerts_boxes/SnackBar'

function TopMidSide({ data = {} }) {
  const { adjustMany, jsn, signal } = React.useContext(UseAppContext)
  const { map_names } = data || {}
  const nn = map_names || []
  const names = nn.length > 0 ? nn : undefined

  const [errors, setErrors] = React.useState(null)
  const [count, setCount] = React.useState(9)

  const adjustView = (k) => {
    const { assets_exchange, map_provision, disco_provision } = jsn || {}
    if (assets_exchange && map_provision && disco_provision) {
      const itm2 = map_prov(assets_exchange, k, map_provision, disco_provision)

      adjustMany({ cbs: itm2, dsk: k.toLowerCase(), signal: 'MAP' })
    } else setErrors(`No view found for: ${k}`)
  }
  const inc = () => setCount((pre) => pre + 9)
  const dec = () => setCount((pre) => pre - 9)
  const clear = () =>
    adjustMany({ cbs: clean_up_data(jsn), signal: null, dsk: null })

  return (
    <Paper
      sx={{
        width: '11.5rem',
        height: '100vh',
        zIndex: 10,
        left: 0,
        position: 'absolute',
        borderRadius: 0,
        padding: 2,
      }}
      elevation={12}
    >
      <Box
        sx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          width: '100%',
        }}
      >
        <img src={assets.logo_dark} alt={'Logo img'} width={125} height={80} />
      </Box>

      <Box
        sx={{
          display: 'flex',
          justifyContent: 'space-around',
          alignItems: 'center',
        }}
      >
        <Typography
          sx={{
            textAlign: 'center',
            margin: '0.5rem 0',
            color: COLORS.primary,
          }}
        >
          MAP LABEL: <strong>{names?.length || 0}</strong>
        </Typography>

        {signal === 'MAP' && (
          <IconButton onClick={clear}>
            <Sync sx={{ fontSize: '0.9rem' }} />
          </IconButton>
        )}
      </Box>

      <Box
        sx={{
          height: '68vh',
          marginTop: 1.5,
          overflow: 'auto',
          marginBottom: 1,
        }}
      >
        {names &&
          names
            .slice(count - 9, count)
            .map((d, i) => (
              <DSK
                key={i}
                name={d && d.toLowerCase()}
                title={d}
                Press={(k) => adjustView(k)}
              />
            ))}
      </Box>

      <Box
        sx={{
          backgroundColor: COLORS.white,
          display: 'center',
          justifyContent: 'center',
          alignItems: 'center',
        }}
      >
        <GoPreview Press={dec} disable={count <= 9} />
        <GoPreview
          Press={inc}
          Icon={ArrowForward}
          disable={count >= names.length}
        />
      </Box>

      {errors && <ErrorSnackbar msg={errors} cancel={() => setErrors(null)} />}
    </Paper>
  )
}

export default TopMidSide
