import { Box } from '@mui/material'
import { HorizontalBar, Bar } from 'react-chartjs-2'
import { COLORS } from '../../../../constants'

export const HorizontalChart = ({
  DATA0 = [330, 80],
  LABELs0 = ['Test 1', 'Test 2'],
  bg = 'transparent',
  style,
  DATA1 = [310, 180],
  LABEL0 = 'Label 1',
  LABEL1 = 'Label 2',
  OPT,
  HEIGHT = 290,
  WIDTH = 600,
  bg0 = 'rgb(74, 20, 140,0.6)',
  ln = COLORS.primary,
  ln1 = COLORS.light_primary,
  bg1 = 'rgba(215, 189, 223, 0.7)',
}) => {
  return (
    <Box sx={{ background: bg }}>
      <HorizontalBar
        height={HEIGHT}
        width={WIDTH}
        data={{
          labels: LABELs0,
          datasets: [
            {
              label: LABEL0,
              data: DATA0,
              backgroundColor: bg0,
              borderColor: ln,
              borderWidth: 1,
              borderSkipped: false,
              ...style,
            },
            {
              label: LABEL1,
              data: DATA1,
              backgroundColor: bg1,
              borderColor: ln1,
              borderWidth: 1,
              borderSkipped: false,
              ...style,
            },
          ],
        }}
        options={{
          maintainAspectRatio: false,
          scales: {
            yAxes: [
              {
                ticks: { beginAtZero: true },
              },
            ],
          },
          legend: { display: false, labels: true },
          ...OPT,
        }}
      />
    </Box>
  )
}

