import Grid from '@mui/material/Grid'
import Box from '@mui/material/Box'
import React from 'react'
import SelectAutoWidth from '../../../../component/input/SelectComponent'
import TextInput from '../../../../component/input/TextInput'
import { BTN } from '../../../../component/input/InputEssential'
import { IconButton, Typography } from '@mui/material'
import Close from '@mui/icons-material/Close'
import Loading from '../../../../component/alerts_boxes/Loading'
import { ErrorSnackbar } from '../../../../component/alerts_boxes/SnackBar'
import connect from '../../../../context/connect_to_server'
import { STATES } from '../../../../constants/coutries'

const dt = new Date()
const dd = dt.getDate() < 10 ? `0${dt.getDate()}` : dt.getDate()
const mm = dt.getMonth() < 10 ? `0${dt.getMonth()}` : dt.getMonth()
const yy = dt.getFullYear()
const et = `${yy}-${mm}-${dd}`

function CustomerDataSet({ cancel }) {
  //   const [loading, setLoading] = React.useContext(false)
  //   const [errors, setErrors] = React.useContext(null)
  const [form, setForm] = React.useState({
    customer_id: '',
    meter_number: '',
    province: '',
    meter_installed: '',
    create_at: et,
    allocation_date: et,
    installation_date: et,
    disco: '',
    loading: false,
    errors: null,
  })
  const {
    customer_id,
    meter_number,
    province,
    allocation_date,
    installation_date,
    meter_installed,
    create_at,
    disco,
    loading,
    errors,
  } = form
  const onChange = (name, value) => setForm({ ...form, [name]: value })

  const submit = async () => {
    setForm({ ...form, loading: false, errors: null })
    const res = await connect.GET({
      urlRoute: 'form74',
      params: 'search=file',
      data: { ...form, errors: null, loading: false },
    })
    if (res.success) {
      setForm({ ...form, loading: false })
    } else {
      setForm({ ...form, loading: false, errors: res.error })
    }
  }

  return (
    <Box marginY={10}>
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
        }}
      >
        <Box>
          <Typography component={'h4'} variant={'h6'} marginTop={2}>
            PROPERTY DATASET MODULE.
          </Typography>
          <Typography component={'p'} variant={'subtitle1'}>
            Please note, your request limit has been set to 20,000.
          </Typography>
        </Box>
        <IconButton onClick={cancel}>
          <Close sx={{ color: 'red' }} />
        </IconButton>
      </Box>
      <Grid container spacing={2}>
        <Grid item xs={4}>
          <SelectAutoWidth
            handleInputChange={onChange}
            name={'disco'}
            value={disco}
            label={'Select Parent Company'}
            items={[
              'Voters Card',
              'Driver Licence',
              'International Passport',
              'Certificate of Incorporation',
              'Other',
            ]}
            size={'100%'}
          />
        </Grid>
        <INPUT
          onChange={onChange}
          name={'customer_id'}
          value={customer_id}
          label={'Property Unique ID'}
        />
      </Grid>

      <Grid container spacing={2}>
        <INPUT
          onChange={onChange}
          name={'meter_number'}
          value={meter_number}
          label={'Meter Number'}
        />
        <Grid item xs={4}>
          <SelectAutoWidth
            handleInputChange={onChange}
            name={'province'}
            value={province}
            label={'Select Property Location State/Province'}
            items={STATES}
            size={'100%'}
          />
        </Grid>
      </Grid>

      <Grid container spacing={2}>
        <Grid item xs={4}>
          <SelectAutoWidth
            handleInputChange={onChange}
            name={'meter_installed'}
            value={meter_installed}
            label={'Select Installation Status *'}
            items={[
              'Voters Card',
              'Driver Licence',
              'International Passport',
              'Certificate of Incorporation',
              'Other',
            ]}
            size={'100%'}
          />
        </Grid>
        <INPUT
          onChange={onChange}
          name={'create_at'}
          value={create_at}
          required={true}
          label={'Creation Date'}
          input_type={'date'}
        />
      </Grid>

      <Grid container spacing={2}>
        <INPUT
          onChange={onChange}
          name={'allocation_date'}
          value={allocation_date}
          required={true}
          label={'Meter Allocation Date'}
          input_type={'date'}
        />

        <INPUT
          onChange={onChange}
          name={'installation_date'}
          value={installation_date}
          required={true}
          label={'Meter Installation Date'}
          input_type={'date'}
        />
      </Grid>

      {loading && <Loading open={loading} />}
      {errors && <ErrorSnackbar msg={errors} />}

      <BTN title="Send" onSubmit={submit} />
    </Box>
  )
}

function INPUT({
  onChange,
  name,
  value,
  required,
  span = 4,
  label,
  input_type,
}) {
  return (
    <Grid item xs={span}>
      <TextInput
        onChange={onChange}
        name={name}
        value={value}
        required={required}
        label={label}
        input_type={input_type}
      />
    </Grid>
  )
}

export default CustomerDataSet
