import Box from '@mui/material/Box'
import React from 'react'
import { BTN } from '../../../../component/input/InputEssential'
import { IconButton, Typography } from '@mui/material'
import Cancel from '@mui/icons-material/CancelOutlined'
import ModelSelect from '../../../../component/input/ModelSelect'
import { STATES } from '../../../../constants/coutries'

function Franchises({ parent_form, onPageChange }) {
  const [form, setForm] = React.useState({ ...parent_form.coverage_data })
  const { franchise_states } = form
  const getPickItem = (name, value) =>
    setForm({ ...form, franchise_states: [...franchise_states, value] })

  const removeItem = (c) =>
    setForm({
      ...form,
      franchise_states: franchise_states.filter((d) => d !== c),
    })
  return (
    <Box marginY={2}>
      <Typography variant="h6" component={'p'} marginTop={2}>
        Add Coverage Area
      </Typography>

      <ModelSelect
        title="Add Franchise Area"
        onChange={getPickItem}
        items={STATES}
      />

      {franchise_states.length > 0 && (
        <Box
          style={{
            display: 'grid',
            gridTemplateColumns: 'repeat(5,1fr)',
            justifyContent: 'flex-start',
            alignItems: 'center',
            border: '1px solid #ccc',
            padding: '0.5rem',
            width: '80%',
            margin: '0.8rem 0',
          }}
        >
          {franchise_states?.map((d, i) => (
            <Item key={i} title={d} onPress={() => removeItem(d)} />
          ))}
        </Box>
      )}

      <BTN
        onSubmit={() =>
          onPageChange('finish', true, undefined, 'coverage_data', form)
        }
        onCancel={() =>
          onPageChange('oth_info', true, true, 'coverage_data', form)
        }
        // disabled={!role || !abbrv || !right_to_share_profile}
      />
    </Box>
  )
}

function Item({ title, onPress }) {
  if (!title) return <></>
  return (
    <Box
      style={{
        display: 'flex',
        justifyContent: 'flex-start',
        alignItems: 'center',
      }}
    >
      <Typography variant="body1" component={'p'}>
        {title}
      </Typography>
      <IconButton onClick={onPress}>
        <Cancel sx={{ color: 'red' }} />
      </IconButton>
    </Box>
  )
}

export default Franchises
