import './App.css'
import UseAppState from './context/UseAppState'
import PageLoadRender from './routes/PageLoadRender'
import { createTheme, ThemeProvider } from '@mui/material'
import { COLORS } from './constants'

const theme = createTheme({
  palette: {
    secondary: { main: COLORS.secondary },
    primary: { main: COLORS.primary },
  },
  components: {
    MuiTypography: {
      variants: [
        {
          props: {
            variant: 'body2',
          },
          style: { fontSize: 11 },
        },
        {
          props: {
            variant: 'body3',
          },
          style: { fontSize: 9 },
        },
      ],
    },
  },
})

function App() {
  return (
    <UseAppState>
      <ThemeProvider theme={theme}>
        <PageLoadRender />
      </ThemeProvider>
    </UseAppState>
  )
}

export default App
