import React, { useReducer } from 'react'
import {
  REQUIRED_ITEMS,
  REMOVE_REQUIRED_ITEMS,
  LOADING,
  AJDUSTMANY,
} from './MagicString'
import useAppContext from './useAppContext'
import useAppReducer from './useAppReducer'

const UseAppState = ({ children }) => {
  const initialStates = {
    required_items: [],
    loading: false,
    isAuthenticated: false,
    data: null,
    errors: null,
    success: null,
    cbs: null,
    jsn: null,
    active: null,
    bude: true,
    user: null,
    goto: {},
    store_items: null,
    list: null,
    address: {},
    bio_data: {},
    building_desc: {},
    ownership_desc: {},
    meter_info: {},
    view_user: null,
    nots: null,
    ms: null,
    dsk: null,
    signal: null,
  }

  const [state, dispatch] = useReducer(useAppReducer, initialStates)

  const addRequiredItem = (item) =>
    dispatch({ type: REQUIRED_ITEMS, payload: item })

  const removeRequiredItem = (item) =>
    dispatch({ type: REMOVE_REQUIRED_ITEMS, payload: item })

  const setLoading = (boo = true) => dispatch({ type: LOADING, payload: boo })
  const adjustMany = (value) =>
    dispatch({
      type: AJDUSTMANY,
      payload: value,
    })

  return (
    <useAppContext.Provider
      value={{
        required_items: state.required_items,
        loading: state.loading,
        data: state.data,
        bude: state.bude,
        user: state.user,
        isAuthenticated: state.isAuthenticated,
        goto: state.goto,
        active: state.active,
        store_items: state.store_items,
        list: state.list,
        address: state.address,
        bio_data: state.bio_data,
        building_desc: state.building_desc,
        ownership_desc: state.ownership_desc,
        meter_info: state.meter_info,
        view_user: state.view_user,
        ms: state.ms,
        nots: state.nots,
        dsk: state.dsk,
        jsn: state.jsn,
        cbs: state.cbs,
        signal: state.signal,
        addRequiredItem,
        removeRequiredItem,
        setLoading,
        adjustMany,
      }}
    >
      {children}
    </useAppContext.Provider>
  )
}

export default UseAppState
