import { GetUnique, Sum } from '../../../../helper/essential'

export const clean_up_data = (data) => {
  const {
    map_,
    disco,
    disco_total_uploads,
    map_total_uploads,
    total_meter,
    map_allocation,
    disco_allocations,
    total_meter_applicant,
    metered_applicant,
    unmetered_applicant,
    metersOnTransitCount,
    unAllocatedMeterCount,
    installation_count,
    disco_provision,
    map_provision,
    assets_exchange,
    // ...res
  } = data || {}

  //   PHASE
  const single_sent = Sum(map_provision.map((d) => d.single_phase_sent))
  const single_phase_in_store = Sum(
    map_provision.map((d) => d.single_phase_in_store)
  )

  const three_sent = Sum(map_provision.map((d) => d.three_phase_sent))
  const three_phase_in_store = Sum(
    map_provision.map((d) => d.three_phase_in_store)
  )

  // ************************************************
  const single_phase_received = Sum(
    disco_provision.map((d) => d.single_phase_received)
  )
  const three_received = Sum(disco_provision.map((d) => d.three_phase_received))

  const single = {
    sent: single_sent,
    received: single_phase_received,
    total: single_phase_in_store + single_sent,
    instore: single_phase_in_store,
  }
  const three = {
    sent: three_sent,
    received: three_received,
    total: three_phase_in_store + three_sent,
    instore: three_phase_in_store,
  }

  //   NAMES
  const map_names = GetUnique(map_provision.map((d) => d.uploadedBy))
  const disco_names = GetUnique(disco_provision.map((d) => d.receiver))

  //      TRANSACTION
  const trans_unique = GetUnique(assets_exchange.map((d) => d.provider))
  const transaction = []

  trans_unique.forEach((c) => {
    const db = assets_exchange.filter((d) => d.provider === c)
    if (db.length > 0) {
      const ea = {
        provider: c,
        receiver: 'DISCO',
        sent: Sum(db.map((x) => x.total_sent)),
        received: Sum(db.map((x) => x.total_received)),
        single: Sum(db.map((x) => x.single)),
        three: Sum(db.map((x) => x.three)),
      }
      transaction.push(ea)
    }
  })

  return {
    header_stats: {
      map_count: map_,
      disco_count: disco,
      disco_total_uploads,
      map_total_uploads,
      total_meter,
      map_allocation,
      disco_allocations,
      total_meter_applicant,
      metered_applicant,
      unmetered_applicant,
      metersOnTransitCount,
      unAllocatedMeterCount,
      installation_count,
    },
    transaction,
    phase: { single, three },
    map_names,
    disco_names,
  }
}

export const disco_prov = (
  data = [],
  name = '',
  support = [],
  oposite = [],
  request = []
) => {
  const item = data.filter((r) => r.receiver === name)
  const rq = request.filter((r) => r.doc_owner === name)
  const brgt = oposite.filter((r) => r.receiver === name)
  const sent = Sum(item.map((d) => d.total_sent))
  const received = Sum(item.map((d) => d.total_received))

  //   REQUEST
  const reqst = Sum(rq.map((s) => s.customer_count))
  const reqst_alo = Sum(rq.map((s) => s.customer_allocated_stats))

  const brgt_instore = Sum(brgt.map((d) => d.total_in_store))
  //   DISCO
  const supp = support.filter((r) => r.receiver === name)
  const received_usage = []

  item.forEach((v) => {
    const join = supp.filter((c) => c.provider === v.uploadedBy)
    if (join.length > 0) {
      received_usage.push(join[0])
    }
  })

  const ds_alo_three = Sum(received_usage.map((d) => d.three_phase_sent))
  const ds_alo_two = Sum(received_usage.map((d) => d.single_phase_sent))
  const ds_insl_three = Sum(
    received_usage.map((d) => d.three_phase_sent_and_install)
  )
  const ds_insl_two = Sum(
    received_usage.map((d) => d.single_phase_sent_and_install)
  )

  const allocate = ds_alo_three + ds_alo_two
  const install = ds_insl_three + ds_insl_two

  const map_names = GetUnique(oposite.map((d) => d.uploadedBy))
  const disco_names = GetUnique(support.map((d) => d.receiver))
  // I ALTERNATE / SWITCH GRAPH CONFIG
  const transaction = item.map((d) => {
    return {
      receiver: d.receiver,
      provider: d.provider,
      label0: d.receiver,
      label1: d.provider,
      sent: d.total_sent,
      received: d.total_received,
    }
  })

  //   console.log(transaction)

  return {
    header_stats: {
      total_meter: sent,
      map_count: 1,
      disco_count: item.length,
      disco_total_uploads: received,
      map_total_uploads: sent,
      metersOnTransitCount: sent - received,
      map_allocation: sent,
      unAllocatedMeterCount: brgt_instore,
      total_meter_applicant: reqst,
      disco_allocations: reqst_alo > 0 ? reqst_alo : allocate,
      metered_applicant: 0,
      unmetered_applicant: allocate - install,
      installation_count: install,
    },
    transaction,
    phase: {
      single: Sum(item.map((d) => d.single)),
      total: Sum(item.map((d) => d.single)) + Sum(item.map((d) => d.three)),
      three: Sum(item.map((d) => d.three)),
    },

    disco_names,
    map_names,
  }
}
export const map_prov = (data = [], name = '', oposite = [], support = []) => {
  const item = data.filter((r) => r.provider === name)
  const brgt = oposite.filter((r) => r.uploadedBy === name)
  const sent = Sum(item.map((d) => d.total_sent))
  const received = Sum(item.map((d) => d.total_received))

  const brgt_sent = Sum(brgt.map((d) => d.total_sent))
  const brgt_instore = Sum(brgt.map((d) => d.total_in_store))
  const brgt_single = Sum(brgt.map((d) => d.single_phase_sent))
  const brgt_three = Sum(brgt.map((d) => d.three_phase_sent))
  const brgt_single_in_store = Sum(brgt.map((d) => d.single_phase_in_store))
  const brgt_three_in_store = Sum(brgt.map((d) => d.three_phase_in_store))

  //   DISCO
  const supp = support.filter((r) => r.provider === name)
  const received_usage = []

  item.forEach((v) => {
    const join = supp.filter((c) => c.receiver === v.receiver)
    if (join.length > 0) {
      received_usage.push(join[0])
    }
  })

  const ds_alo_three = Sum(received_usage.map((d) => d.three_phase_sent))
  const ds_alo_two = Sum(received_usage.map((d) => d.single_phase_sent))
  const ds_insl_three = Sum(
    received_usage.map((d) => d.three_phase_sent_and_install)
  )
  const ds_insl_two = Sum(
    received_usage.map((d) => d.single_phase_sent_and_install)
  )

  const allocate = ds_alo_three + ds_alo_two
  const install = ds_insl_three + ds_insl_two

  const single = {
    sent: brgt_single,
    received: Sum(received_usage.map((d) => d.single_phase_received)),
    instore: brgt_single_in_store,
    total: brgt_single_in_store + brgt_single,
  }

  const three = {
    sent: brgt_three,
    received: Sum(received_usage.map((d) => d.three_phase_received)),
    instore: brgt_three_in_store,
    total: brgt_three_in_store + brgt_three,
  }

  const map_names = GetUnique(oposite.map((d) => d.uploadedBy))
  const disco_names = GetUnique(support.map((d) => d.receiver))
  // I ALTERNATE / SWITCH GRAPH CONFIG
  const transaction = item.map((d) => {
    return {
      receiver: d.provider,
      provider: d.receiver,
      label0: d.receiver,
      label1: d.provider,
      sent: d.total_sent,
      received: d.total_received,
    }
  })

  //   console.log(transaction)

  return {
    header_stats: {
      total_meter: brgt_sent,
      map_count: 1,
      disco_count: item.length,
      disco_total_uploads: received,
      map_total_uploads: sent,
      metersOnTransitCount: brgt_sent - received,
      map_allocation: brgt_sent,
      unAllocatedMeterCount: brgt_instore,
      total_meter_applicant: 0,
      disco_allocations: allocate,
      metered_applicant: 0,
      unmetered_applicant: allocate - install,
      installation_count: install,
    },
    transaction,
    phase: {
      single,
      three,
    },

    disco_names,
    map_names,
  }
}
