import axios from 'axios'

function Connect(url) {
  this.url = url || 'https://qu-essence.com'
}

Connect.prototype.setAuth = async function () {
  return {
    setToken: async (token) => localStorage.setItem('_', JSON.stringify(token)),
    getToken: async () => {
      const k = localStorage.getItem('_')
      if (k) {
        return JSON.parse(k)
      } else return null
    },
    clearToken: async () => localStorage.removeItem('_'),
  }
}

Connect.prototype.encryptToken = async function (token) {
  ;(await this.setAuth()).setToken(token)

  return `Key saved successfully...`
}

Connect.prototype.decryptToken = async function () {
  const k = await (await this.setAuth()).getToken()
  this.token = k

  return 'Key decrptyed successfully...'
}

Connect.prototype.create_queries = function (obj) {
  if (!obj) return ''
  // let keys = ''
  let aggs = '&'
  const key = Object.keys(obj)
  const value = Object.values(obj)

  for (let i = 0; i < key.length; i++) {
    if (i < key.length - 1) {
      if (value[i]) {
        aggs += `${key[i]}=${value[i]}&`
      }
      // keys += `${key[i]},`
    } else {
      if (value[i]) {
        aggs += `${key[i]}=${value[i]}`
        // keys += `${key[i]}`
      }
    }
  }
  return aggs.length === 1 ? undefined : aggs
}

Connect.prototype.definedEssential = async function (
  type = { 'Content-Type': 'application/json' },
  params,
  data,
  method,
  hash
) {
  !this.token && (await this.decryptToken())
  const tk = hash ? hash : this.token
  const headerConfig = {
    headers: {
      type,
      ...(tk ? { authorization: `Bearer ${tk}` } : ''),
    },
  }

  const que = method === 'GET' && data && this.create_queries(data)
  const qre =
    params && que
      ? `${params}${que}`
      : params && !que
      ? params
      : que && !params
      ? que.slice(1, que.length)
      : undefined

  return { header: headerConfig, params: qre ? `?${qre}` : '' }
}

Connect.prototype.methods = async function ({
  urlRoute,
  data,
  params,
  method,
  type,
  hash,
}) {
  if (urlRoute !== 'user/login' && hash) {
    await this.encryptToken(hash)
  }

  let rs = { success: false, error: null, data: null }
  const { header, params: q } = await this.definedEssential(
    type,
    params,
    data,
    method.toUpperCase(),
    hash
  )

  const url = `${this.url}/api/v1/${urlRoute}${q}`
  const it_is = method === 'get'

  try {
    const {
      data: { success, data: info },
    } = it_is
      ? await axios[method](url, header)
      : await axios[method](url, data, header)

    rs.success = success
    rs.data = info
  } catch (error) {
    const err =
      error.response !== undefined
        ? error.response.data.error
        : JSON.stringify(error)

    rs.error = err
  }

  return rs
}

Connect.prototype.GET = async function ({ urlRoute, data, params, hash }) {
  return await this.methods({ urlRoute, data, params, method: 'get', hash })
}

Connect.prototype.POST = async function ({ urlRoute, data, params, hash }) {
  return await this.methods({ urlRoute, data, params, method: 'post', hash })
}

Connect.prototype.PUT = async function ({ urlRoute, data, params, hash }) {
  return await this.methods({ urlRoute, data, params, method: 'put', hash })
}

Connect.prototype.loadUser = async function () {
  return await this.methods({
    urlRoute: 'user/90?search=DECODE',
    method: 'get',
  })
}

Connect.prototype.clearAll = async function () {
  this.token = null
  await (await this.setAuth()).clearToken()
}

Connect.prototype.POSTIMG = async function ({ urlRoute, data, params, hash }) {
  const formData = new FormData()
  formData.append('file', data)
  return await this.methods({
    urlRoute,
    data: formData,
    params,
    method: 'post',
    type: { 'Content-Type': 'multipart/form-data' },
    hash,
  })
}

Connect.prototype.PUTIMG = async function ({ urlRoute, data, params, hash }) {
  const formData = new FormData()
  formData.append('file', data)
  return await this.methods({
    urlRoute,
    data: formData,
    params,
    method: 'put',
    type: { 'Content-Type': 'multipart/form-data' },
    hash,
  })
}

Connect.prototype.img = function (img_string) {
  if (!img_string || img_string === 'undefined') return undefined
  return `${this.url}/public/uploads/${img_string}`
}

// const connect = new Connect(`http://localhost:5530`)
const connect = new Connect()

export default connect
