import React from 'react'
import AddUser from '@mui/icons-material/PersonAddAlt1TwoTone'
import Chart from '@mui/icons-material/BarChart'
// import User from '@mui/icons-material/Person'
// import Suspended from '@mui/icons-material/PersonAddDisabled'
import Divider from '@mui/material/Divider'
import LI from '../../component/bars/drawer/LI'
import ListParent from '../../component/bars/drawer/ListParent'
import { useNavigate } from 'react-router-dom'
import UseAppContext from '../../context/useAppContext'

function AdsSide({ open }) {
  const { user, active, adjustMany } = React.useContext(UseAppContext)
  const { user_key } = user || {}
  const navigate = useNavigate()
  return (
    <ListParent>
      <LI
        open={open}
        text={'Dashboard'}
        active={active === 'dashboard'}
        Icon={Chart}
        onPress={() => {
          adjustMany({ active: 'dashboard' })
          return navigate(`/`)
        }}
      />
      <Divider />
      <LI
        open={open}
        text={'Add User'}
        Icon={AddUser}
        active={active === 'adduser'}
        onPress={() => {
          adjustMany({ active: 'adduser' })
          navigate(`/${user_key}/adduser`)
        }}
      />
      <Divider />
    </ListParent>
  )
}

export default AdsSide
