import { Send } from '@mui/icons-material'
import { Box, Button, Typography } from '@mui/material'
import React from 'react'
import Loading from '../../../component/alerts_boxes/Loading'
import { ErrorSnackbar } from '../../../component/alerts_boxes/SnackBar'
import WrappedInBars from '../../../component/bars/WrappedInBars'
import connect from '../../../context/connect_to_server'
import UseAppContext from '../../../context/useAppContext'
import DsSide from '../DsSide'
import { csvDownload, uploadTemplate } from './CsvFile'
import { UploadFileContainer } from './csvTils'

function DiscoCreateCustomerCSV() {
  const { adjustMany } = React.useContext(UseAppContext)

  const [form, setForm] = React.useState({
    edit: true,
    path: '',
    loading: false,
    errors: null,
    success: null,
  })

  React.useEffect(() => {
    ;(function () {
      adjustMany({ active: 'customer' })
    })()

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const { path, loading, errors } = form

  const onChange = (name, value) => setForm({ ...form, [name]: value })

  return (
    <WrappedInBars UL={DsSide}>
      <Box marginY={2}>
        <div>
          <div style={{ padding: 5 }}>
            <div>
              <Typography variant="h5" component={'h6'} marginY={1}>
                Create Many Customer Records
              </Typography>
            </div>

            <div style={{ color: 'red', lineHeight: 1.7 }}>
              {/* <p>*File Extention should be .csv format*</p> */}
              <p>
                *Make sure you use camelCase for your headers e.g "firstName"*
              </p>
              <Typography component={'p'} variant={'body1'}>
                *Headers much contain: ['fname', 'lname', 'streetName', 'city',
                'busStop', 'state', 'lga', 'nationality','landMark',
                'buildingType','email', 'identityType', 'identityID',
                'preferredMeterPhase' and 'requestType' ] as they're all
                required fields*
              </Typography>
            </div>

            <Button
              variant="outlined"
              onClick={() =>
                csvDownload({
                  data: uploadTemplate.CUSTOMER,
                  structure: (row) => row,
                  headings: uploadTemplate.CUSTOMER0,
                  title: `Customer Record Upload Template`,
                })
              }
            >
              Download Template
              <i className="fas fa-book" style={{ marginLeft: 10 }}></i>
            </Button>

            {/* BTN WITH DOTTED*/}
            <UploadFileContainer
              name={'path'}
              value={path}
              onChange={onChange}
            />

            <div
              style={{
                width: '100%',
                textAlign: 'center',
                padding: 15,
              }}
            >
              <h5>
                Don't delete any row title in the template before file upload?
              </h5>
            </div>

            <Button
              startIcon={<Send />}
              disabled={!path}
              variant="contained"
              sx={{ marginY: 1.5 }}
              onClick={async () => {
                setForm({
                  ...form,
                  loading: true,
                  errors: null,
                  success: null,
                })
                const res = await connect.POSTIMG({
                  urlRoute: 'form74/csv-upload',
                  data: path,
                  params: 'search=CUSTOMER',
                })

                if (res.success) {
                  setForm({ ...form, loading: false, success: res.data })
                } else setForm({ ...form, loading: false, errors: res.error })
              }}
            >
              Upload File
            </Button>
          </div>
        </div>

        {loading && <Loading open={loading} />}
        {errors && <ErrorSnackbar msg={errors} />}
      </Box>
    </WrappedInBars>
  )
}

export default DiscoCreateCustomerCSV
