import TableCell from '@mui/material/TableCell'
import TableRow from '@mui/material/TableRow'
import { Box, IconButton, TablePagination, Tooltip } from '@mui/material'
import React from 'react'
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye'
import TableTemplate from '../../../component/tables/simple_table/TableTemplate'
import { COLORS } from '../../../constants'

const hdr = [
  'PropertyID',
  'Applicant FullName',
  'Telephone',
  'Building Address',
  'Allocation Status',
  'Field Status',
  'Request Date',
  'Action',
]

const bd = [
  {
    fullName: 'Logs Title',
    telephone: '39e',
    buildingAddress: '38',
    hasAllocation: '3',
    isCertified: '892',
    createAt: 'Now',
    meterNumber: '694',
    customer_id: '9',
  },
]
// slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)map((row, index).
export default function TableActivities({ header = hdr, row = bd, onPress }) {
  const rows = row && Object.keys(row).toString().startsWith(0) ? row : [row]
  const [page, setPage] = React.useState(0)
  const [rowsPerPage, setRowsPerPage] = React.useState(5)

  // React.useEffect(() => {
  //   ;(function () {
  //     console.log('Hi and loading')
  //   })()
  // }, [])

  const handleChangePage = (event, newPage) => {
    setPage(newPage)
  }

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10))
    setPage(0)
  }

  return (
    <Box sx={{ width: '100%' }}>
      <TableTemplate header={header}>
        {rows
          .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
          .map((d, i) => (
            <TableRow
              key={i}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              {/* cartonID, meterOwner, storeID, destinationStore,
                        phase, manufacturerName, defaultUnit, pop */}
              <TableCell> {d.customer_id} </TableCell>
              <TableCell> {d.fullName} </TableCell>
              <TableCell> {d.telephone} </TableCell>
              <TableCell> {d.buildingAddress} </TableCell>
              <TableCell
                sx={{ color: !d.hasAllocation ? 'orange' : COLORS.green_dark }}
              >
                {' '}
                {d.hasAllocation
                  ? `Allocated:${d.meterNumber}`
                  : 'No allocation yet'}{' '}
              </TableCell>
              <TableCell
                sx={{
                  color: !d.isCertified ? COLORS.danger : COLORS.green_dark,
                }}
              >
                {d.isCertified ? 'Property verified' : 'Site not verifed'}{' '}
              </TableCell>
              <TableCell> {d.createAt} </TableCell>
              <TableCell>
                <Tooltip title="Assign for Verification">
                  <IconButton
                    onClick={() => onPress(d)}
                    disabled={d.isCertified}
                  >
                    <RemoveRedEyeIcon />
                  </IconButton>
                </Tooltip>
              </TableCell>
            </TableRow>
          ))}
      </TableTemplate>
      <TablePagination
        rowsPerPageOptions={[5, 10, 15, 20]}
        component="div"
        count={rows.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={handleChangePage}
        onRowsPerPageChange={handleChangeRowsPerPage}
      />
    </Box>
  )
}
