import { Box, Divider } from '@mui/material'
import React from 'react'
import WrappedInBars from '../../component/bars/WrappedInBars'
import UseAppContext from '../../context/useAppContext'
import ProfileBodyView from './ProfileBodyView'
import ProfileHeaderView from './ProfileHeaderView'

function ProfileUserView({ Side }) {
  const { user } = React.useContext(UseAppContext)
  // React.useEffect(() => {
  //   ;(function () {
  //     adjustMany({ active: 'dashboard' })
  //   })()
  // // eslint-disable-next-line react-hooks/exhaustive-deps
  // }, [])

  return (
    <WrappedInBars UL={Side}>
      <Box marginY={10}>
        <ProfileHeaderView data={user} Side={Side} />
        <Divider />
        <ProfileBodyView data={user} />
      </Box>
    </WrappedInBars>
  )
}

export default ProfileUserView
