import React from 'react'
import { Grid, Paper, TextField, Button, Typography } from '@mui/material'
import FormControlLabel from '@mui/material/FormControlLabel'
import Checkbox from '@mui/material/Checkbox'
import { assets } from '../constants'
import Loading from '../component/alerts_boxes/Loading'
import {
  ErrorSnackbar,
  SuccessSnackbar,
} from '../component/alerts_boxes/SnackBar'
import connect from '../context/connect_to_server'
import UseAppContext from '../context/useAppContext'

const Login = () => {
  const { adjustMany } = React.useContext(UseAppContext)
  const [password, setPassword] = React.useState('')
  const [email, setEmail] = React.useState('')
  const [errors, setErrors] = React.useState(null)
  const [loading, setLoading] = React.useState(false)
  const [success, setSuccess] = React.useState(null)
  const [show, setShow] = React.useState(null)

  React.useEffect(() => {
    ;(async function () {
      const res = await connect.loadUser()
      if (res.success) {
        adjustMany({ user: res.data, isAuthenticated: true })
      }
    })()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const paperStyle = {
    padding: 20,
    height: '70vh',
    width: 350,
    margin: '0 auto',
    borderRadius: '0.5rem',
  }

  const btnstyle = { margin: '8px 0' }

  return (
    <div
      style={{
        backgroundColor: 'rgba(139, 139, 139, 0.3)',
        height: '100vh',
        padding: '6rem',
      }}
    >
      <Paper style={paperStyle}>
        <Grid align="center" sx={{ marginTop: 1.5 }}>
          <img
            src={assets.logo_dark}
            alt={'Avatar'}
            style={{ width: 60, height: 40 }}
          />
          <h2>Sign In</h2>
        </Grid>
        <TextField
          label="Username"
          placeholder="Enter username"
          fullWidth
          required
          sx={{ marginY: 1 }}
          onChange={(tg) => setEmail(tg.target.value)}
          value={email}
        />
        <TextField
          label="Password"
          placeholder="Enter password"
          type={show ? 'text' : 'password'}
          fullWidth
          required
          sx={{ marginY: 1 }}
          onChange={(tg) => setPassword(tg.target.value)}
          value={password}
        />
        <FormControlLabel
          control={
            <Checkbox
              name="checkedB"
              color="primary"
              onChange={() => setShow(!show)}
            />
          }
          label={show ? 'Hide Password' : 'Show Password'}
        />
        <Button
          type="submit"
          color="primary"
          variant="contained"
          style={btnstyle}
          fullWidth
          onClick={async () => {
            setErrors(null)
            setSuccess(null)
            if (password && email) {
              setLoading(true)
              const res = await connect.POST({
                urlRoute: 'user/login',
                data: { password, email },
              })

              if (res.success) {
                const rs = await connect.GET({
                  urlRoute: `user/${email}`,
                  hash: res.data,
                })

                if (rs.success) {
                  const { user_key, ...uk } = rs.data
                  adjustMany({
                    user: { ...uk, user_key },
                    isAuthenticated: true,
                  })
                  setLoading(false)
                } else {
                  setErrors(rs.error)
                  setLoading(false)
                }
              } else {
                setErrors(res.error)
                setLoading(false)
              }
            } else setErrors('Both email and password fields are required')
          }}
        >
          Sign in
        </Button>
        <Typography sx={{ fontSize: '0.8rem' }}>
          Kindly contact project admin incase you forgot your password?
        </Typography>
      </Paper>

      {loading && <Loading open={loading} />}
      {success && (
        <SuccessSnackbar msg={success} cancel={() => setSuccess(null)} />
      )}
      {errors && <ErrorSnackbar msg={errors} cancel={() => setErrors(null)} />}
    </div>
  )
}

export default Login
